import {default as createService} from '../customers/create/create.service';
import { throws } from 'assert';

class AdminController {

  pageNumber = 1;
  pageSize = 10;
  custDetails;
  customerSelected;
  auditData = [];
  auDataAvail = false;

    constructor(createService, AdminService) {
      this.createService = createService;
      this.AdminService = AdminService;
      this.name = 'admin';

      this.getCustomerDetails();
    }

    getCustomerDetails(){
      this.AdminService.getAllCustomer( this.customerId , this.pageNumber, this.pageSize).then((response) => {
        //console.log(response);
        if(response.data.serviceStatus == 'SUCCESS'){
          //console.log("found cust data "+ response.data.serviceResponse);
          this.custDetails = response.data.serviceResponse.customerList;
         // console.log("cust Details " + this.custDetails);
        }
      });
    }


    getAuditDetails(){
      this.auDataAvail = false;
    //  console.log("customer selected :" + this.customerSelected);
      this.AdminService.getAuditDetails(this.customerSelected).then((response) => {
        //console.log("Audit response "+ response);
        if(response.data.serviceStatus == 'SUCCESS'){
          this.auDataAvail = true;
          let res = JSON.parse(response.data.serviceResponse);
          for(var i=0; i< res.length; i++){
              this.auditData[i] = res[i];
          }
        }else{
          alert(response.data.serviceResponse);
        }
        
      // console.log(this.auditData);
      });
    }
    
  }
  
  AdminController.$inject=['createService','AdminService'];
  export default AdminController;


