class demoController{


    form = {
        formName: null,
        contacts: []
    };

    constructor(){
        this.name='demo';
    }


    addContact(){
        this.form.contacts.push({
            id: ((this.form.contacts != undefined)? (this.form.contacts.length+1):0),
            name: null
        });
    }
}

export default demoController;


