import * as appProperties from "../../../config.properties";

class RequestService{

    constructor($http) {
        "ngInject"
        this.name = "requestService";
        this.http = $http;

    }


    getAllRequest(pageNumber,pageSize){

      
        let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL+'/api/viewRequest',
           
            headers: {
                'content-type': 'application/json'
            },
            params:{
                pageNumber :pageNumber,
                pageSize : pageSize
            }	
         }
         return this.http(requestString);

     }

     getCutomer(id){

        var payload = new FormData();
        payload.append('id', id);

       
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL+'/api/getCutomer',
            processData: false,
            headers:{
                'content-type':undefined
            },
           data:payload
        })

     }

     getBR(id){
//debugger;
        var payload = new FormData();
        payload.append('id', id);

        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL+'/api/byId',
            processData: false,
            headers:{
                'content-type':undefined
            },
           data:payload
        })

     }

     getCP(id){

        var payload = new FormData();
        payload.append('id', id);

        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL+'/api/cpById',
            processData: false,
            headers:{
                'content-type':undefined
            },
           data:payload
        })

     }

     getTranche(id){

        var payload = new FormData();
        payload.append('id', id);

        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL+'/api/getTrancheById',
            processData: false,
            headers:{
                'content-type':undefined
            },
           data:payload
        })

     }

     getDeal(id){

        var payload = new FormData();
        payload.append('id', id);

        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL+'/api/getDealbyId',
            processData: false,
            headers:{
                'content-type':undefined
            },
           data:payload
        })

     }

     approveRequest(request){
         
       
        let requestString = {
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/checker',
            headers: {
                'content-type': 'application/json'
            },

            data: request

        }
        $(".se-pre-con").show();
        return this.http(requestString)
     }

     updateRequestData(identity){

        var payload = new FormData();
        payload.append('identity', identity);
       
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL+'/api/requestFor',
            processData: false,
            headers:{
                'content-type':undefined
            },
           data:payload
        })

     }
   
}

export default RequestService;


