import angular from 'angular';
import uiRouter from 'angular-ui-router';
import quarterlyreportComponent from './quarterlyreport.component';
import manageComponent from './manage/manage.component';
import quarterlyService from './quarterlyreport.service';

let quarterlyreportModule = angular.module('quarterlyreport', [
  uiRouter
])

.component('quarterlyreport', quarterlyreportComponent)
.component('qmanage', manageComponent)
.service('quarterlyReportService', quarterlyService)
.config(($stateProvider, $urlRouterProvider) => {
  "ngInject";

  $stateProvider
    .state({
      name: 'quarterlyreport',
      url: '/quarterlyreport',
      component: 'quarterlyreport'
    }).state({
      name: 'quarterlyreport.manage',
      url: '/manage',
      component: 'qmanage'
    })
})

.name;

export default quarterlyreportModule;



