import angular from 'angular';
import uiRouter from 'angular-ui-router';
import creditdebitComponent from './creditdebit.component';
import manageCrDRComponent from '../CreditDebit/manage/managecrdr.component';
import feesCalculationComponent from '../CreditDebit/feescalculation/feescalculation.component';
import { default  as CreditDebitService } from './creditdebit.service';
import feesCalculationService from './feescalculation/feescalculation.service';
require('angucomplete-alt');

let creditDebitModule = angular.module('creditdebit',
[ 
    uiRouter,
    "angucomplete-alt"
])
.config( ($stateProvider) =>{
    "ngInject";
    $stateProvider
    .state('creditdebit', {
        url: '/creditdebit',
        component : 'creditdebit'
    }).state('managecrdr', {
        url: '/creditdebit/managecrdr',
        component : 'manageCrDRComponent'
    })
    .state('feescalculation', {
        url: '/credidebit/feescalculation',
        component: 'feesCalculationComponent'
    });
})
.component('creditdebit', creditdebitComponent)
.component('manageCrDRComponent', manageCrDRComponent)
.component('feesCalculationComponent', feesCalculationComponent)
.service('CreditDebitService',CreditDebitService)
.service('feesCalculationService', feesCalculationService)
.name;

export default creditDebitModule;


