import * as appProperties from "../../../config.properties";

class BrService{

    constructor($http){
        "ngInject"
        this.name="brService";
        this.http=$http;
        
    }

    

    addBRDetails(form){
     
        $(".se-pre-con").show();
        return this.http({
                    method: 'POST',
                    url: appProperties.CONFIG.BASE_URL+'/api/addBR',
                    headers: {
                    'content-type':undefined
                    },
    
                    data:form
                })

    }

    uploadBRDocument(brDoc,trancheId,dealNumber,customerId){

        var payload=new FormData();
        payload.append('brDoc',brDoc[0])

        payload.append('trancheId', trancheId);
        payload.append('dealNumber', dealNumber);
        payload.append('customerId', customerId)

        
        return this.http({

                method: 'POST',
                url: appProperties.CONFIG.BASE_URL+'/api/uploadDocument',
                processData: false,
                headers:{
                        'content-type':undefined
                },

                data:payload

                })

    }

    updateBRDetails(form){
        $(".se-pre-con").show();
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL+'/api/updateBR',
            headers: {
            'content-type': undefined
            },
            
            data:form
        })
    }
   
    downloadDocument(fileName,customerId){
        
       
        return this.http({
            method: 'Post',
            url: appProperties.CONFIG.BASE_URL+'/api/downloadDoc',
            processData: false,
            headers:{
                'content-type': 'application/json'
            },
            params:{
                "fileName":fileName,
                "customerId":customerId
            },
            responseType:'blob'
           
        })
       
    }

}

export default BrService;


