import angular from 'angular';
import uiRouter from 'angular-ui-router';
import customerCreateComponent from './create.component'
import {default as CreateService} from './create.service';

var moment = require('moment');

let customerCreateModule = angular.module('create', [
    uiRouter,
    
])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
      .state('customers/create/reg', {
        url: '/customers/create/reg',
        component: 'create',
        // params: {
        //   'customer': null
        // }
      }).state('customers/edit', {
        url: '/customers/edit',
        component: 'create',
        params: {
          'customer': null
        }
      });
  })
  .directive("filesInput", function() {
    return {
      require: "ngModel",
      link: function postLink(scope,elem,attrs,ngModel) {
        elem.on("change", function(e) {
          var files = elem[0].files;
          ngModel.$setViewValue(files);
        })
      }
    }
  })
 

  .component('create', customerCreateComponent)
  .service('createService',CreateService)
