import * as appProperties from "../../../config.properties";
import commonService from "../../../common.service";

class manageaccController {

    pagenumber = 1;
    pageSize = 10;
    masterArray = [];
    arrayToDisplay;

    constructor(manageAccService, $state, commonService) {
        "ngInject"

        this.baseUrl = appProperties.CONFIG.BASE_URL;
        this.state = $state;
        this.manageAccService = manageAccService;
        // this.name = "request";

        this.commonService = commonService;
        this.role = this.commonService.rolename;
        this.userName = this.commonService.username;
        this.IdList = this.commonService.IdList;

        this.myStorage = window.localStorage;
        if (this.commonService.rolename != undefined && this.commonService.username) {

            this.myStorage.setItem("role", this.commonService.rolename);
            this.myStorage.setItem("userName", this.commonService.username);
            this.myStorage.setItem("IdList", this.commonService.IdList);
        }

        // this.getAllRequest();
        this.getAllPendingRequest();

    }

    getAllRequest() {
        $(".se-pre-con").show();
        this.manageAccService.getAllRequest(this.pagenumber, this.pageSize).then((response) => {

            $(".se-pre-con").hide();
            this.disable = false;
            if (response.data.serviceResponse != null && response.data.serviceStatus == "SUCCESS") {

                this.pgNo = this.pagenumber;
                this.pgSize = this.pageSize;
                this.arrayToDisplay = response.data.serviceResponse;
                this.arrayToDisplay = data;

            } else if (response.data.serviceError == 'No Record found') {
                this.disable = true;
            } else {

            }
        })
    }

    getAllPendingRequest() {
        $(".se-pre-con").show();
        this.manageAccService.getAllPendingRequest(this.pagenumber, this.pageSize).then((response) => {

            $(".se-pre-con").hide();
            this.disable = false;
            if ( response.data.serviceStatus == "SUCCESS") {

                this.pgNo = this.pagenumber;
                this.pgSize = this.pageSize;
                //this.arrayToDisplay = response.data.serviceResponse;
                var creditAccNo;
                var creditAccIfscCode;
                var data = response.data.serviceResponse;
                for (var i = 0; i < data.length; i++) {
                    var bankTypeSelected = data[i]['bankSelected'];
                    if (bankTypeSelected == 'axis') {
                        var axisAccNo = data[i]['axisAccountNumber'];
                        var axisAccIfscCode = data[i]['axisIfscCode'];
                        if (axisAccNo != "" || axisAccNo != null || axisAccIfscCode != "" || axisAccIfscCode != null) {
                            creditAccNo = axisAccNo;
                            creditAccIfscCode = axisAccIfscCode;
                            data[i]['creditAccNo'] = creditAccNo;
                            data[i]['creditAccIfscCode'] = creditAccIfscCode;
                        }

                    } else {
                        creditAccNo = data[i]['otherBankAccount'];
                        creditAccIfscCode = data[i]['otherBankIfscCode'];
                        data[i]['creditAccNo'] = creditAccNo;
                        data[i]['creditAccIfscCode'] = creditAccIfscCode;
                    }
                }
                this.arrayToDisplay = data;

            } else if (response.data.serviceError == 'No Record found') {
                this.disable = true;
            } else {

            }
        })
    }

    approveRequest(request) {
        $(".se-pre-con").show();
        this.manageAccService.approveRequest(request).then((response) => {
            $(".se-pre-con").hide();

            let respObj;
            if (response.data.serviceStatus == 'SUCCESS') {
                respObj = {
                    "type": "info",
                    "msg": "Request successful."
                };
            } else {
                respObj = {
                    "type": "warn",
                    "msg": response.data.serviceError
                };
            }

            this.commonService.displayMsg(respObj);
            this.getAllPendingRequest();
        })
    }

    rejectRequest(request) {

        var confirm = prompt('Enter comment for Reject Request');


        if (confirm == null) {
            return;
        }

        if (confirm == '') {
            alert("Comment is required for Reject Request...!");
            return;
        }

        // var status = 'Rejected';
        // request.checkerId = this.myStorage.getItem("userName");
        // request.status = status;
        request.comments = confirm;

        this.manageAccService.rejectRequest(request).then((response) => {

            $(".se-pre-con").hide();
            if (response.data.serviceStatus == 'SUCCESS') {
                // window.location.reload();
                alert('Request rejected for ' + request.id);
            } else {
                alert("Error :" + response.data.serviceError);
            }
            this.getAllPendingRequest();
        })
    }

    numberOfPages() {
        return Math.ceil(this.masterArray.length / this.pageSize);
    }

    jumpToFirst() {
        this.pagenumber = 1;
        // this.getAllRequest()
        this.getAllPendingRequest();
    }

    previous() {
        this.pagenumber--;
        // this.getAllRequest()
        this.getAllPendingRequest();
    }

    next() {
        this.pagenumber++;
        // this.getAllRequest()
        this.getAllPendingRequest();
    }

}
//RequestController.$inject = ['requestService', '$state', 'commonService'];
export default manageaccController;


