import * as appProperties from "../../../config.properties";

class ManageAccService
{

    constructor($http) {
        "ngInject"
        this.name = "manageAccService";
        this.http = $http;

    }


    getAllRequest(pageNumber,pageSize){
        let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL+'/api/account_master/getAllTransactions',
           
            headers: {
                'content-type': 'application/json'
            },
            params:{
                pageNumber :pageNumber,
                pageSize : pageSize
            }	
         }
         return this.http(requestString);

     }

     getAllPendingRequest(pageNumber,pageSize){
        let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL+'/api/account_master/getAllPendingTransactions',
           
            headers: {
                'content-type': 'application/json'
            },
            params:{
                pageNumber :pageNumber,
                pageSize : pageSize
            }	
         }
         return this.http(requestString);

     }


    approveRequest(request){
        let requestString = {
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/account_master/approveTransaction',
            headers: {
                'content-type': 'application/json'
            },
            data: request
        }
        //$(".se-pre-con").show();
        return this.http(requestString)
     }

     rejectRequest(request){
        let requestString = {
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/account_master/rejectTransaction',
            headers: {
                'content-type': 'application/json'
            },

            data: request

        }
        $(".se-pre-con").show();
        return this.http(requestString)
     }
   
   
}

export default ManageAccService;


