    getRatingsDetailsByCpAndTrancheId(cp, tranche) {

        this.securityService.getRatingsDetailsByCpAndTrancheId(cp, tranche).then((response) => {
            // console.log(response);
            $(".se-pre-con").hide();

            if (response.data.serviceStatus == 'SUCCESS') {
                var data = response.data.serviceResponse;
                self.ratingList = data;
                // Start Here

                for (var i = 0; i < self.ratingList.length; i++) {

                    if (i == 0) {
                        this.fstamt = self.ratingList[i].creditRatingAmount
                    } else if (i = 1) {
                        this.secamt = self.ratingList[i].creditRatingAmount
                    }
                }
                if (this.fstamt > this.secamt) {
                    self.isRatingLower = self.ratingList[1].id;
                    self.form.RatingDataUsed = self.ratingList[1];
                } else if (this.fstamt == this.secamt) {

                    if (self.ratingAgencyName != undefined || self.ratingAgencyName != null) {
                        self.ratingList.forEach((rating) => {
                            if (self.ratingAgencyName == rating.creditRatingAgency) {
                                self.isRatingLower = rating.id;
                                self.form.RatingDataUsed = rating;
                            }
                        })
                    }
                    else {
                        self.isRatingLower = self.ratingList[0].id;
                        self.form.RatingDataUsed = self.ratingList[0]
                    }
                } else {
                    self.isRatingLower = self.ratingList[0].id;
                    self.form.RatingDataUsed = self.ratingList[0];
                }

                self.form.ratingAgencyName = self.form.RatingDataUsed.creditRatingAgency;
                self.form.currentCreditRating = self.form.RatingDataUsed.creditRating;
                self.form.validityOfIssuance = self.form.RatingDataUsed.validityOfIssuance,
                    self.form.validityPeriod = self.form.RatingDataUsed.validityPeriod;

                if (self.form.RatingDataUsed.issuancePeriodIn == 'Date') {
                    self.form.issuancePeriodIn = '';
                } else {
                    self.form.issuancePeriodIn = self.form.RatingDataUsed.issuancePeriodIn;
                }
                if (self.form.RatingDataUsed.ratingPeriodIn == 'Date') {
                    self.form.ratingPeriodIn = '';
                } else {
                    self.form.ratingPeriodIn = self.form.RatingDataUsed.ratingPeriodIn;
                }

                self.oldRating = self.form.RatingDataUsed;
                var brData = self.data.customer.br
                brData.forEach((element) => {
                    self.tempBrData = element;

                });
                if (self.data.tranche != null && self.data.tranche.length > 0) {
                    var trancheData = self.data.tranche;
                    var amount = 0;
                    var prev = 0;
                }
                // End Here

            } else {
                alert(response.data.serviceStatus);
            }

            //  console.log(this.ratingList);  
        });
    }

