    getIsinDetails(item) {
        //  debugger;
        console.log(item);
        $(".nq-ipa-tr-bd-matdate").attr("disabled", false);
        if (item) {
            self.scope.$broadcast('angucomplete-alt:changeInput', 'isinNumber', '');
            //console.log("Data: " +self.data);
            // console.log(self.data);
            if (self.data.serviceResponse != 'null') {
                if (item.originalObject.maturityDate != null) {
                    self.form.maturityDate = item.originalObject.maturityDate;
                    $(".nq-ipa-tr-bd-matdate").attr("disabled", true);

                    // console.log(self.data.ratings);
                    self.validityPeriodOfRatingExpired = false;
                    let ratingFound = self.data.ratings;
                    let matDt = self.form.maturityDate;
                    for (var i = 0; i < ratingFound.length; i++) {
                        //console.log(ratingFound[i].dateOfRating + " - "+ ratingFound[i].validityPeriod);
                        let dtOfRating = ratingFound[i].dateOfRating;
                        let expiryDate;
                        if (ratingFound[i].ratingPeriodIn === 'Year') {
                            this.expiryDate = moment(dtOfRating, 'DD/MM/YYYY').add(ratingFound[i].validityPeriod, 'year').format('DD/MM/YYYY');
                            //console.log("Exp dt :" + this.expiryDate);
                            if (moment(matDt,"DD/MM/YYYY").isAfter(moment(this.expiryDate,"DD/MM/YYYY"))) {
                                self.validityPeriodOfRatingExpired = true;
                            }
                        }

                        if (ratingFound[i].ratingPeriodIn === 'Date') {
                            this.expiryDate = moment(ratingFound[i].validityPeriod).format('DD/MM/YYYY');
                            console.log("Exp dt :" + this.expiryDate);
                            if (moment(matDt, "DD/MM/YYYY").isAfter(moment(this.expiryDate, "DD/MM/YYYY"))) {
                                self.validityPeriodOfRatingExpired = true;
                            }
                        }
                    }
                    if (self.validityPeriodOfRatingExpired) {
                        alert("Validity Period of Rating is expired");
                         self.form.maturityDate = "";
                        return;
                    } else {
                        console.log('have rating validity period');
                    }
                } else {
                    self.form.maturityDate = "";
                }
            }
        } 

    }



