    validateOfferLetterDate() {

        var valueDate = moment(this.form.valueDate, 'DD/MM/YYYY');
        var offerLetterDated = moment(this.form.offerLetterDated, 'DD/MM/YYYY');
        var diffDays = valueDate.diff(offerLetterDated, 'days');

        if (offerLetterDated > valueDate) {
            alert("Offer Letter Date is less than Value date...!");
            this.form.offerLetterDated = null;
            return;
        }

        if (diffDays > 14) {
            alert("Difference between Offer Letter Date and Value Date should be Less than 14 Days...!");
            this.form.offerLetterDated = null;
            return;
        }
    }


