    validateTotalAmount() {

        var sumOfTranch;
        var remainAmount = 0;

        let maxAmount = parseInt(10000000000);
        if (typeof this.form.totalAmount != 'number') {

            if (!this.form.totalAmount.includes(','))
                this.form.totalAmount = this.form.totalAmount;
            else
                this.form.totalAmount = parseInt(this.form.totalAmount.toString().replace(/,/g, ''));
        }

        // console.log("total amt found: " + this.form.totalAmount);
        // console.log("Ratings found " + self.ratingList.length);
        if (this.form.totalAmount >= maxAmount) {
            if (self.ratingList.length < 2) {
                alert("Amount is greater than or equal to 1000 Cr. , requires two ratings");
                this.form.totalAmount = null;
                return;
            }
        } else {
            console.log("In else : Total Amt found " + this.form.totalAmount + " <= " + maxAmount + " with Ratings found : " + self.ratingList.length);

        }

        if (this.data.commercialPaper && this.data.commercialPaper.cpAmount < this.form.totalAmount) {
            alert("You can't enter Total Amount more than CP Amount");
            this.form.totalAmount = this.form.totalAmount.toString().slice(0, -1);
            return;
        }
        else if (this.data.cpAmount < this.form.totalAmount) {
            alert("You can't enter Total Amount more than CP Amount");
            this.form.totalAmount = this.form.totalAmount.toString().slice(0, -1);
            return;
        }

        if (tempCPnumber == undefined || tempCPnumber == null || tempCPnumber == '') {
            tempCPnumber = self.data.cpNumber;
        }

        this.securityService.sumOfTranche(this.form.valueDate, tempCPnumber).then((response) => {

            if (response.data.serviceStatus == 'SUCCESS') {
                sumOfTranch = response.data.serviceResponse;

                if (sumOfTranch != null) {
                    self.previousOutstandingAmount = sumOfTranch;
                    if (this.data.commercialPaper) {
                        remainAmount = this.data.commercialPaper.cpAmount - sumOfTranch;
                    }
                    else {
                        remainAmount = this.data.cpAmount - sumOfTranch;
                    }

                    if (self.previousvalue != undefined || self.previousvalue != null) {
                        remainAmount = remainAmount + self.previousvalue;
                    }
                }
                else {
                    self.previousOutstandingAmount = 0;
                    sumOfTranch = 0;

                    if (this.data.commercialPaper) {
                        remainAmount = this.data.commercialPaper.cpAmount;
                    }
                    else {
                        remainAmount = this.data.cpAmount;
                    }
                }

                $(".se-pre-con").hide();

                if (remainAmount >= parseInt(this.form.totalAmount)) {
                    if (typeof this.form.totalAmount != 'number') {
                        if (!this.form.totalAmount.includes(','))
                            this.form.totalAmount = this.form.totalAmount;
                        else
                            this.form.totalAmount = parseInt(this.form.totalAmount.toString().replace(/,/g, ''));
                    }

                    if (self.previousvalue != undefined || self.previousvalue != null) {
                        this.cpOutstandingAmount = parseInt(this.form.totalAmount);
                    }
                    else {
                        this.cpOutstandingAmount = parseInt(this.form.totalAmount) + sumOfTranch;
                    }
                }
                else {
                    if (remainAmount > 0)
                        alert("You have only RS. " + remainAmount + " balance");
                    else
                        alert("You have only RS. " + 0 + " balance");
                    // debugger;
                    this.form.totalAmount = this.form.totalAmount.toString().slice(0, -1);
                    if (typeof this.form.totalAmount != 'number') {
                        if (!this.form.totalAmount.includes(','))
                            this.form.totalAmount = this.form.totalAmount;
                        else
                            this.form.totalAmount = parseInt(this.form.totalAmount.toString().replace(/,/g, ''));
                    }

                    if (self.previousvalue != undefined || self.previousvalue != null) {
                        this.cpOutstandingAmount = parseInt(this.form.totalAmount);
                    }
                    else {
                        this.cpOutstandingAmount = parseInt(this.form.totalAmount) + sumOfTranch;
                    }
                    return;
                }
            }
            else {
                alert("Internal Server error");
                return;
            }
        })
            .catch(err => {
                console.log('mainerror', err);
            });
    }

