import * as appProperties from "../../../config.properties";

var self;

class ReportController{

    customerId;
    dealNumber;
    deals={  };
    constructor(reportService){
     
        this.baseUrl=appProperties.CONFIG.BASE_URL;
        
        this.reportService=reportService;
        this.name = "report";
        self=this;
    }

    inputChanged(item){
       
        if(item){
            
            
            self.data=item.originalObject;
            this.deals=self.data

            this.customerId=this.deals.customerId;
            this.dealNumber=this.deals.dealNumber;
  
        }
    }

  
    getDeal(customerId){
       
        if(customerId!=null){

            this.reportService.getDeal(customerId).then((response)=>{
               
                
                if(response.data.serviceStatus=='SUCCESS'){
                   
                    this.deals=response.data.serviceResponse;
                  
                    
                }
                else if(response.data.serviceStatus=='FAIL'){
                   
                   alert("No Deal Matching with this "+this.customerId+" customer Id!")

                }else{
                    
                    alert("Something went wrong...!")
                } 

            });

        }else{
            alert("Please Enter Customer Id...!");
        }
    }

    
    downloadReport(dealNumber){
       
       
        if(dealNumber!=null){
          
            this.reportService.writeReport(dealNumber).then((response)=>{
              
                if(response.status=='200' && response.data.size>0){

                    
                    var blob=response;
    
                    var binaryData = [];
                    binaryData.push(response.binaryData);
                    let a = document.createElement("a");
                    a.style = "display: none";
                    document.body.appendChild(a);
                  
                    var url = window.URL.createObjectURL(new Blob([response.data], { type: "application/msword"}))
                   
                    a.href = url;
                    a.download = 'report.xlsx';
                    a.click();
                    window.URL.revokeObjectURL(url);
                

                }else{
                   
                    alert("Something went wrong - OR - You Enter Wrong Deal Number...Please Check... Can't Download Report...!");
                    this.downloadingXLS = false;
                }
               
            });

        }else{

           alert("Please Enter Deal Number...!");

        }

       

    }

}
ReportController.$inject = ['reportService'];
export default ReportController;


