import * as appProperties from "../../../config.properties";
class ReportDownloadService {

    constructor($http) {
        "ngInject"
        this.name = "reportDownloadService";
        this.http = $http;
    }


    
    calculateOutstanding(id){
       return this.http({
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL + '/api/calculateOutstanding/'+id,
            headers: {
                'content-type': 'application/json'
            },
            responseType: 'application/json'
        });
       }
    
    getReport(ipaReportCode, ipaStartDate, ipaEndDate) {
        return this.http({
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL + '/api/ipaReportDownload',
            headers: {
                'content-type': undefined
            },
            params: {
                "reportCode": ipaReportCode,
                "frmDate": ipaStartDate,
                "toDate": ipaEndDate
            },
            responseType: 'arraybuffer'

        })

    }

    downloadInvoiceReport(reportData) {
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/downloadInvoiceReport',
            header: {
                'content-type': undefined
            },
            params: {
                'reportData': reportData
            },
            responseType: 'blob'
        });
    }

    getTrancheDetails(reportData){
        debugger;
        return this.http({
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL + '/api/getTrancheDetails',
            headers: {
                'content-type': undefined
            },
            params: {
                'reportData': reportData
            }
        })
    }


}

export default ReportDownloadService;


