import * as appProperties from "./config.properties";

class AppService {

    constructor($http) {
        this.name = "appService";
        this.$http = $http;
        this.userUrl=appProperties.CONFIG.Base_user_url;
    }

    getAllUsers() {
        this.$http({
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL+'/api/users/getAllUsers',
            headers: {
                'content-type': 'application/json'
            },
            params: {
                fetchOnlyActive: false,
            }

        }).then(function(response){
        });
    }

    

}

export default AppService;


