import * as appProperties from "../../../config.properties";

class ShowBrService{
    
        constructor($http){
            "ngInject"
            this.name = "showBrService";
            this.http=$http;
            
        }
    
        getBRDetails(customerId,pageNumber,pageSize){
            if (customerId == undefined || customerId == null) {
                customerId = "";
            }
            let requestString = {
                method: 'GET',
                url: appProperties.CONFIG.BASE_URL+'/api/viewAllBR',
                headers: {
                    'content-type': 'application/json'
                },
                params:{
                    "pageNumber" :pageNumber,
                    "pageSize" : pageSize,
                    "customerId":customerId
                }
            }

            return this.http(requestString);

        }

        getBrByCustomerId(customerId,pageNumber, pageSize){
            console.log(appProperties.CONFIG.BASE_URL+ '/api/getRequestDetails');
            this.isinNumber = ""
            let requestString={
                method:'POST',
                url:appProperties.CONFIG.BASE_URL+'/api/getRequestDetails',
                headers:{
                    'content-type': 'application/json'
                },
                params:{
                    "customerId":customerId,
                    "modelName":"BR",
                    pageNumber: pageNumber,
                    pageSize: pageSize,
                    "isinNumber": this.isinNumber
                }
            }
        return this.http(requestString);
        }       
    }
    
    export default ShowBrService;


