import * as properties from '../../config.properties'

class DashboardController {

  constructor($http, commonService) {

    "ngInject"

    this.$http = $http;
    this.baseURL = properties.CONFIG.BASE_URL + "/api/";
    this.commonService = commonService;
    this.getDashboardData();
  }

  getDashboardData() {
    let resp = this.$http({
      method: "GET",
      url: this.baseURL + "getNofitications",
      headers: {
        'content-type': 'application/json'
      }
    });

    $(".se-pre-con").show();
    resp.then((response) => {
      if (response.data.serviceStatus && response.data.serviceStatus.toLowerCase() === "success") {

        this.dashDataArr = response.data.serviceResponse;
        this.prepareData();
        $(".se-pre-con").hide();

      }
      else if (response.data.serviceStatus && response.data.serviceStatus.toLowerCase() === "fail") {
        // show error somewhere
        $(".se-pre-con").hide();
      }
    });
  }

  prepareData()
  {
    this.finalDataArr = [];
    const dataObj = {};

    this.dashDataArr.forEach((e, i) => {
      
    });
  }
}

export default DashboardController;


