import angular from 'angular';
import uiRouter from 'angular-ui-router';
import buybackComponent from './buyback.component';
import createComponent from './create/create.component';
import manageComponent from './manage/manage.component';
import buyBackService from './buyback.service'

let buybackModule = angular.module('buyback', [
  uiRouter
])

.component('buycreate', createComponent)
.component('buymanage', manageComponent)
.component('buyback', buybackComponent)
.service('buyBackService', buyBackService)
.config(($stateProvider, $urlRouterProvider) => {
  "ngInject";

  $stateProvider
    .state({
      name: 'buyback',
      url: '/buyback',
      component: 'buyback'
    }).state({
      url: '/create',
      name: 'buyback.create',
      component: 'buycreate'
    })
    .state({
      url: '/edit',
      name: 'buyback.edit',
      params: {
        edit: true,
        view: false,
        buyback: null,
        chk: false
      },
      component: 'buycreate'
    }).state({
      name: 'buyback.manage',
      url: '/manage',
      component: 'buymanage'
    })
})

.name;

export default buybackModule;



