import angular from 'angular';
import uiRouter from 'angular-ui-router';
import investorComponent from './investor.component';
import createComponent from './create/create.component';
import manageComponent from './manage/manage.component';
import investorService from './investor.service';

let investorModule = angular.module('investor', [
  uiRouter
])

  .component('invcreate', createComponent)
  .component('invmanage', manageComponent)
  .component('investor', investorComponent)
  .service('investorService', investorService)

  .config(($stateProvider, $urlRouterProvider) => {
    "ngInject";

    $stateProvider
      .state({
        name: 'investor',
        url: '/investor',
        component: 'investor'
      }).state({
        url: '/create',
        name: 'investor.create',
        component: 'invcreate'
      })
      .state({
        url: '/edit',
        name: 'investor.edit',
        params: {
          edit: true,
          view: false,
          investor: null,
          chk: false
        },
        component: 'invcreate'
      }).state({
        name: 'investor.manage',
        url: '/manage',
        component: 'invmanage'
      })
  })
  .name;

export default investorModule;



