import angular from 'angular';
import uiRouter from 'angular-ui-router';
import brDetailsComponent from './br.component';
import {default as BrService} from './br.service';
require('angucomplete-alt');
var moment = require('moment');

let brDetailsModule=angular.module('br',[
    uiRouter,
    "angucomplete-alt",
])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
    .state('boardResolution/br',{
        url:'/boardResolution/br',
        component:'brComponent'
    }).state('boardResolution/edit',{
        url:'/boardResolution/edit',
        component:'brComponent',
        params:{
            'br':null
        }
    });

})


.component('brComponent',brDetailsComponent)
.service('brService',BrService)
.name;


export default brDetailsModule;




