import angular from 'angular';
import uiRouter from 'angular-ui-router';
import showCPDetailsComponent from './cp_show.component'
import {default as ShowCPService} from './cp_show.service';

let showCPDetailsModule = angular.module('cp_show', [
    uiRouter,
    
])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
      .state('cpReg/showCp', {
        url: '/cpReg/showCp',
        component: 'cpShow'
      });
  })

  .directive('toggle', function(){
    return {
      restrict: 'A',
      link: function(scope, element, attrs){
        if (attrs.toggle=="tooltip"){
          $(element).tooltip();
        }
        if (attrs.toggle=="popover"){
          $(element).popover();
        }
      }
    };
  })

  .component('cpShow', showCPDetailsComponent)
  .service('showCPService',ShowCPService)
  .name;


  export default showCPDetailsModule;


