import * as appProperties from "../../../config.properties";

class RequestController {

    pagenumber = 1;
    pageSize = 10;
    masterArray = [];
    arrayToDisplay;

    constructor(requestService, $state, commonService, buyBackService, investorService, benposService, quarterlyReportService) {
        "ngInject"
        this.benposService = benposService
        this.buyBackService = buyBackService
        this.investorService = investorService
        this.qrService = quarterlyReportService;
        this.baseUrl = appProperties.CONFIG.BASE_URL;
        this.state = $state;
        this.requestService = requestService;
        this.name = "request";

        this.commonService = commonService;
        this.role = this.commonService.rolename;
        this.userName = this.commonService.username;
        this.IdList = this.commonService.IdList;

        this.myStorage = window.localStorage;
        if (this.commonService.rolename != undefined && this.commonService.username) {

            this.myStorage.setItem("role", this.commonService.rolename);
            this.myStorage.setItem("userName", this.commonService.username);
            this.myStorage.setItem("IdList", this.commonService.IdList);
        }



        this.getAllRequest();


    }

    getAllRequest() {
        $(".se-pre-con").show();
        this.requestService.getAllRequest(this.pagenumber, this.pageSize).then((response) => {
            $(".se-pre-con").hide();
            this.disable = false;

            if (response.data.serviceResponse != null && response.data.serviceStatus == "SUCCESS") {

                this.pgNo = this.pagenumber;
                this.pgSize = this.pageSize;


                this.arrayToDisplay = response.data.serviceResponse.Request;


            } else if (response.data.serviceError == 'No Record found') {

                this.disable = true;


            } else {

            }
        })
    }


    numberOfPages() {
        return Math.ceil(this.masterArray.length / this.pageSize);
    }

    jumpToFirst() {
        this.pagenumber = 1;
        this.getAllRequest()
    }

    previous() {
        this.pagenumber--;
        this.getAllRequest()
    }

    next() {
        this.pagenumber++;
        this.getAllRequest()
    }

    viewRequest(request, $state) {



        if (request.requestType == 'Customer_Registration') {

            this.requestService.getCutomer(request.identity).then((response) => {

                this.customer = response.data.serviceResponse;
                this.state.go("customers/edit", { 'customer': this.customer });

            })

        }
        else if (request.requestType == 'Br_Registration') {

            this.requestService.getBR(request.identity).then((response) => {

                this.br = response.data.serviceResponse;
                this.state.go("boardResolution/edit", { 'br': this.br })

            })

        }
        else if (request.requestType == 'Cp_Registration') {

            this.requestService.getCP(request.identity).then((response) => {

                this.cplist = response.data.serviceResponse;
                this.state.go("cpReg/editCP", { 'cplist': this.cplist });

            })

        }
        else if (request.requestType == 'Tranche_Registration') {

            this.requestService.getTranche(request.identity).then((response) => {

                this.tranch = response.data.serviceResponse;
                this.state.go("security/editTranch", { 'tranch': this.tranch });

            })

        }
        else if (request.requestType == 'Deal_Registration') {

            this.requestService.getDeal(request.identity).then((response) => {

                this.deal = response.data.serviceResponse;
                this.state.go("deal/editDeal", { 'deal': this.deal });

            })

        }
        else if (request.requestType == 'Update_Customer') {


            this.requestService.updateRequestData(request.identity).then((response) => {


                var obj = JSON.parse(response.data.serviceResponse.updatedData);
                this.customer = obj;

                this.state.go("customers/edit", { 'customer': this.customer });

            })

        }
        else if (request.requestType == 'Update_Br') {


            this.requestService.updateRequestData(request.identity).then((response) => {


                var obj = JSON.parse(response.data.serviceResponse.updatedData);
                this.br = obj;

                this.state.go("boardResolution/edit", { 'br': this.br })

            })

        }
        else if (request.requestType == 'Update_CP') {


            this.requestService.updateRequestData(request.identity).then((response) => {


                var obj = JSON.parse(response.data.serviceResponse.updatedData);

                this.cplist = obj;

                this.state.go("cpReg/editCP", { 'cplist': this.cplist });

            })
            // this.state.go("cpReg/editCP/edit", { 'cplist': this.cplist });
        }
        else if (request.requestType == 'Update_Tranche') {


            this.requestService.updateRequestData(request.identity).then((response) => {


                var obj = JSON.parse(response.data.serviceResponse.updatedData);
                this.tranch = obj;


                this.requestService.getTranche(this.tranch.id).then((response) => {


                    this.tranch.cp = response.data.serviceResponse.cp;

                    this.state.go("security/editTranch", { 'tranch': this.tranch });
                })

                // this.state.go("security/editTranch",{ 'tranch':this.tranch } );

            })

        }
        else if (request.requestType == 'Update_Deal') {



            this.requestService.updateRequestData(request.identity).then((response) => {

                var obj = JSON.parse(response.data.serviceResponse.updatedData);
                this.deal = obj;

                this.requestService.getDeal(this.deal.id).then((response) => {

                    this.deal.tranche = response.data.serviceResponse.tranche


                    this.state.go("deal/editDeal", { 'deal': this.deal });
                })

            })

        }
        else if (request.requestType == "UPDATE_INVESTOR") {
            this.requestService.updateRequestData(request.identity).then((response) => {
                let investor = JSON.parse(response.data.serviceResponse.updatedData)
                this.state.go("investor.edit", {
                    edit: true,
                    view: true,
                    investor: investor,
                    chk: true
                })
            })

        }
        else if (request.requestType == "REGISTER_INVESTOR") {
            this.investorService.getById(request.identity).then((response) => {
                this.state.go("investor.edit", {
                    edit: true,
                    view: true,
                    investor: response.data,
                    chk: true
                })
            })

        }
        else if (request.requestType == "UPDATE_BUYBACK") {
            this.requestService.updateRequestData(request.identity).then((response) => {

                let buyback = JSON.parse(response.data.serviceResponse.updatedData)
                this.state.go("buyback.edit", {
                    edit: true,
                    view: true,
                    buyback: buyback,
                    chk: true
                })
            })

        } else if (request.requestType == "REGISTER_BUYBACK") {
            this.buyBackService.getById(request.identity).then((response) => {
                this.state.go("buyback.edit", {
                    edit: true,
                    view: true,
                    buyback: response.data,
                    chk: true
                })
            })

        }
        else if (request.requestType == "REGISTER_BENOPS") {
            this.benposService.downloadDoc(request.identity)
                .then((response) => {
                    let fileName = response.headers("Content-Disposition").split(";").pop().split("filename=", 2).pop()
                    this.buildAndPushFile(response.data, fileName + ".txt");
                })
        }
        else if (request.requestType == "ADD_QR") {
            this.qrService.download(request.identity)
                .then((response) => {
                    let fileName = response.headers("Content-Disposition").split(";").pop().split("filename=", 2).pop()
                    this.buildAndPushFile(response.data, fileName);
                })
        }
    }

    buildAndPushFile(result, name) {
        // be careful of scopes, might cause file leakages
        let file = new Blob([result], {});
        let fileURL = (window.URL || window.webkitURL).createObjectURL(file);
        let link = angular.element('<a/>');
        link.attr({
            href: fileURL,
            download: name
        })[0].click();
    }



    approveRequest(request) {

        var confirm = prompt('Enter comment for Approve Request')

        if (confirm == null) {
            return;
        }
        var status = 'Approved';
        request.checkerId = this.myStorage.getItem("userName");
        request.status = status;
        request.comments = confirm;

        this.requestService.approveRequest(request).then((response) => {

            $(".se-pre-con").hide();
            if (response.data.serviceStatus == 'SUCCESS') {
                // window.location.reload();
                alert('Request Approved for ' + request.requestId);
            }

        })

    }

    callBackRequest(request) {

        var confirm = prompt('Enter comment for Callback Request')


        if (confirm == null) {
            return;
        }

        if (confirm == '') {
            alert("comment is required for CallBack Request...!");
            return;
        } if (confirm == null) {
            return;
        }

        if (confirm == '') {
            alert("comment is required for CallBack Request...!");
            return;
        }


        var status = 'CallBack';
        request.checkerId = this.myStorage.getItem("userName");
        request.status = status;
        request.comments = confirm;

        this.requestService.approveRequest(request).then((response) => {

            $(".se-pre-con").hide();
            if (response.data.serviceStatus == 'SUCCESS') {
                alert('Request callback for ' + request.requestId);
                $(".se-pre-con").hide();
            }

        })



    }

    rejectRequest(request) {

        var confirm = prompt('Enter comment for Reject Request')


        if (confirm == null) {
            return;
        }

        if (confirm == '') {
            alert("comment is required for Reject Request...!");
            return;
        }

        var status = 'Rejected';
        request.checkerId = this.myStorage.getItem("userName");
        request.status = status;
        request.comments = confirm;

        this.requestService.approveRequest(request).then((response) => {

            $(".se-pre-con").hide();
            if (response.data.serviceStatus == 'SUCCESS') {
                // window.location.reload();
                alert('Request rejected for ' + request.requestId);
            }

        })
    }



}
//RequestController.$inject = ['requestService', '$state', 'commonService'];
export default RequestController;


