import angular from 'angular';
import uiRouter from 'angular-ui-router';
import createCPDetailsComponent from './cp_create.component'
import {default as CreateCPService} from './cp_create.service';
require('angucomplete-alt');

var moment = require('moment');

let createCPDetailsModule = angular.module('cp_create', [
    uiRouter,
    "angucomplete-alt",
])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
      .state('cpReg/createCp/cp_create', {
        url: '/cpReg/createCp/cp_create',
        component: 'cpCreate'
      }).state('cpReg/editCP', {
        url: '/cpReg/editCP',
        component: 'cpCreate',
        params:{
          'cplist':null
        }
      });
  })
  .directive('toggle', function(){
    return {
      restrict: 'A',
      link: function(scope, element, attrs){
        if (attrs.toggle=="tooltip"){
          $(element).tooltip();
        }
        if (attrs.toggle=="popover"){
          $(element).popover();
        }
      }
    };
  })

  .component('cpCreate', createCPDetailsComponent)
  .service('createCPService',CreateCPService)
  .name;


  export default createCPDetailsModule;


