import * as appProperties from "../../../config.properties";
import { throws } from "assert";
import { toJson } from "@uirouter/core";

var self;

class ReportDownloadController {
    // ipaReportCode;
    // ipaStartDate;
    // ipaEndDate; 
    outstanding;
    previousOutstanding;

    form = {
        ipaReportCode: null,
        ipaStartDate: null,
        ipaEndDate: null
      
    }

    constructor(reportDownloadService) {
        this.showCalenderOption = true;
        this.reportDownloadService = reportDownloadService;
        this.baseUrl = appProperties.CONFIG.BASE_URL;
        // self = this;
        // this.commonService = commonService;
        // this.myStorage = window.localStorage;
        // if (this.commonService.rolename != undefined && this.commonService.username) {

        //     this.myStorage.setItem("role", this.commonService.rolename);
        //     this.myStorage.setItem("userName", this.commonService.username);
        //     this.myStorage.setItem("IdList", this.commonService.IdList);
        //     this.myStorage.setItem("emailId", this.commonService.emailId);
        // }
        this.showFYDrpDwn = false;
    }


    calculateOutstanding()
    {
        const out = null;
        this.reportDownloadService.calculateOutstanding(this.form.trancheId).then((response) => {

        if(response.status == 404)
        {
            alert("Please Enter Valid Tranche")
        }
        
        if(response.status == 200)
        {
            this.form.outstanding = response.data.outstanding,
            this.form.previousOutstanding = response.data.previous_outstanding
        }
        
        });
       
    }

    downloadReport() {

        this.ipaReportCode = this.form.ipaReportCode
        this.ipaStartDate = this.form.ipaStartDate
        this.ipaEndDate = this.form.ipaEndDate
        if (this.ipaEndDate == null || this.ipaEndDate == '') {
            this.ipaEndDate = this.ipaStartDate
        }

        $(".se-pre-con").show();
        this.reportDownloadService.getReport(this.ipaReportCode, this.ipaStartDate, this.ipaEndDate).then((response) => {
            $(".se-pre-con").hide();

            // setTimeout(()=>{
            //     $(".se-pre-con").hide();
            // }, 3000)


            if (response.data.serviceResponse == 'SUCCESS' && response.data.serviceStatus == '200') {
                // alert("Tranche Registerd Successfully...!");
                // this.state.go('security/details');

            }


            var blob = response;
            var binaryData = [];
            binaryData.push(response.binaryData);
            let a = document.createElement("a");
            a.style = "display: none";
            document.body.appendChild(a);
            var url = window.URL.createObjectURL(new Blob([response.data], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8" }))
            a.href = url;
            a.download = this.ipaStartDate + this.ipaEndDate + '.xls';
            a.click();
            window.URL.revokeObjectURL(url);
        });
    }

    loadInvoiceForm() {
        this.showCalenderOption = false;
        this.showFYDrpDwn = false;
    }

    checkReportType() {
        // console.log('Report code found :' +this.form.ipaReportCode);
        let reportType = this.form.ipaReportCode;
        if (reportType === 'YEARLY') {
            this.showCustomerId = true;
            this.showCalenderOption = false;
            this.showFYDrpDwn = true;
        } else {
            if (reportType === 'DEALWISE') {
                this.showCalenderOption = true;
                this.showCustomerId = true;
                this.showFYDrpDwn = false;
            }
        }
    }

    downloadInvoiceReport() {
        console.log(this.form.ipaReportCode);
        let reportCode = this.form.ipaReportCode;
        if (reportCode === 'YEARLY') {
            if (this.form.customerId === null || this.form.customerId == '') {
                alert("Invoice report - Customer Id is required");
                return;
            }

            if(this.fyYear == null || this.fyYear == ''){
                console.log("Fy Selected" + this.fyYear);
                alert("FY is required");
                return;
            }

            let reportData = {
                "reportCode": this.form.ipaReportCode,
                "customerId": this.form.customerId,
                "fyYear" : this.fyYear

            }

            // $(".se-pre-con").show();
            this.reportDownloadService.downloadInvoiceReport(angular.toJson(reportData)).then((response) => {
                //console.log("yearly report response "+ response.data);
                // $(".se-pre-con").hide;
                console.log(response);
                // if(response.data.serviceStatus == 'SUCCESS'){
                var blob = response;

                var binaryData = [];
                binaryData.push(response.binaryData);
                let a = document.createElement("a");
                a.style = "display: none";
                document.body.appendChild(a);

                var url = window.URL.createObjectURL(new Blob([response.data], { type: "application/pdf" }))
                a.href = url;
                a.download = "deal_repo" + this.form.customerId + '.pdf';
                a.click();
                window.URL.revokeObjectURL(url);
                // }else{
                //     alert(response.data.serviceError);
                // }

            });

        }
        if (reportCode === 'DEALWISE') {
            if (this.form.customerId === null || this.form.customerId == '') {
                alert("Invoice report - Customer Id is required");
                return;
            }
            if (this.form.startDate === '' || this.form.startDate == null) {
                alert("Invoice report - Start date is required");
                return;
            }
            if (this.form.endDate === '' || this.form.endDate == null) {
                alert("Invoice report - End date is required");
                return;
            }

            let reportData = {
                "reportCode": this.form.ipaReportCode,
                "customerId": this.form.customerId,
                "startDate": this.form.startDate,
                "endDate": this.form.endDate
            }
        
            this.reportDownloadService.getTrancheDetails(angular.toJson(reportData)).then((response) =>{
                console.log( "response of tranche details : " + response.data.serviceStatus);
                if(response.data.serviceStatus == 'SUCCESS'){
                    this.reportDownloadService.downloadInvoiceReport(angular.toJson(reportData)).then((response) => {
                        var blob = response;
        
                        var binaryData = [];
                        binaryData.push(response.binaryData);
                        let a = document.createElement("a");
                        a.style = "display: none";
                        document.body.appendChild(a);
        
                        var url = window.URL.createObjectURL(new Blob([response.data], { type: "application/pdf" }))
                        a.href = url;
                        a.download = "deal_repo" + this.form.customerId + '.pdf';
                        a.click();
                        window.URL.revokeObjectURL(url);
                        //   }else{
                        //       alert(response.data.serviceError);
                        //   }
        
                    });
                }else{
                    alert("No tranche details found");

                }
            });
            // $(".se-pre-con").show();
            // this.reportDownloadService.downloadInvoiceReport(angular.toJson(reportData)).then((response) => {
            //     //  $(".se-pre-con").hide;
            //     // console.log("dealwise " + (response));
            //     //   if(response.data.serviceStatus == 'SUCCESS'){
               
            //     var blob = response;

            //     var binaryData = [];
            //     binaryData.push(response.binaryData);
            //     let a = document.createElement("a");
            //     a.style = "display: none";
            //     document.body.appendChild(a);

            //     var url = window.URL.createObjectURL(new Blob([response.data], { type: "application/pdf" }))
            //     a.href = url;
            //     a.download = "deal_repo" + this.form.customerId + '.pdf';
            //     a.click();
            //     window.URL.revokeObjectURL(url);
            //     //   }else{
            //     //       alert(response.data.serviceError);
            //     //   }

            // });
        }
    }
}
ReportDownloadController.$inject = ['reportDownloadService'];

export default ReportDownloadController;


