import angular from 'angular';
import uiRouter from 'angular-ui-router';
import addSecurityComponent from './addsecurity.component'
import {default as AddSecurityService} from './addsecurity.service';
require('angucomplete-alt');

var moment = require('moment');

let addSecurityModule = angular.module('addsecurity', [
    uiRouter,
    "angucomplete-alt",
])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
      .state('security/addsecurity', {
        url: '/security/addsecurity',
        component: 'addSecurity'
      }).state('security/editTranch', {
        url: '/security/editTranch',
        component: 'addSecurity',
        params:{
          'tranch': null
        }
      });
  })

  .component('addSecurity', addSecurityComponent)
  .service('securityService',AddSecurityService)
  .name;


  export default addSecurityModule;
  


