import * as appProperties from "../../../config.properties";

class feesCalculationManagementService
{

    constructor($http) {
        "ngInject"
        this.name = "manageAccService";
        this.http = $http;

    }


    getAllRequest(pageNumber,pageSize){
        console.log("Inside ManageAccService Get All Requests");
      
        let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL+'/api/feescalculation/getAllTransactions',
           
            headers: {
                'content-type': 'application/json'
            },
            params:{
                pageNumber :pageNumber,
                pageSize : pageSize
            }	
         }
         return this.http(requestString);

     }


    approveRequest(request){
        let requestString = {
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/feescalculation/approveTransaction',
            headers: {
                'content-type': 'application/json'
            },
            data: request
        }
        $(".se-pre-con").show();
        return this.http(requestString)
     }

     rejectRequest(request){
        let requestString = {
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/feescalculation/rejectTransaction',
            headers: {
                'content-type': 'application/json'
            },

            data: request

        }
        $(".se-pre-con").show();
        return this.http(requestString)
     }
   
   
}

export default feesCalculationManagementService;


