import angular from 'angular';
import uiRouter from 'angular-ui-router';
import reportDownloadComponent from './reportDownload.component';
import {default as ReportDownloadService} from './reportDownload.service';
require('angucomplete-alt');

let reportDownloadModule=angular.module('reportDownload',[
    uiRouter,
    "angucomplete-alt",
])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
    .state('/report/reportDownload',{
        url:'/report/reportDownload',
        component:'reportDownloadComp'
    });

})
.directive('toggle', function(){
    return {
      restrict: 'A',
      link: function(scope, element, attrs){
        if (attrs.toggle=="tooltip"){
          $(element).tooltip();
        }
        if (attrs.toggle=="popover"){
          $(element).popover();
        }
      }
    };
  })


.component('reportDownloadComp',reportDownloadComponent)
.service('reportDownloadService',ReportDownloadService)
.name;

export default reportDownloadModule;


