import * as appProperties from "../../../config.properties";
class SecurityDetailsService {

    constructor($http) {
        "ngInject"
        this.name = "securityDetailsService";
        this.http = $http;

    }

    getSecurityDetails(pageNumber, pageSize, customerId, isinNumber) {

        if (isinNumber == undefined || isinNumber == null) {
            isinNumber = ""
            if (customerId == undefined || customerId == null) {
                customerId = "";
            }
        }
        if (customerId == undefined || customerId == null) {
            customerId = "";
            if (isinNumber == undefined || isinNumber == null) {
                isinNumber = ""
            }
        }

        let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL + '/api/viewAllTranche',
            headers: {
                'content-type': 'application/json'
            },
            params: {
                pageNumber: pageNumber,
                pageSize: pageSize,
                "customerId": customerId,
                "isinNumber": isinNumber
            }
        }
        return this.http(requestString);
    }

    getTranchByCustomerId(customerId, pageNumber, pageSize, isinNumber) {
        console.log("page sise is - at service--" + pageSize);
        console.log(appProperties.CONFIG.BASE_URL + '/api/getRequestDetails');
        let requestString = {
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/getRequestDetails',
            headers: {
                'content-type': 'application/json'
            },
            params: {
                "customerId": customerId,
                "isinNumber": isinNumber,
                "modelName": "Tranche",
                pageNumber: pageNumber,
                pageSize: pageSize
            }
        }
        return this.http(requestString);
    }

    downloadRedemtionPdf(trancheId, authorityDetails) {
      
        let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL + '/api/downloadRedemtionPdf',
            headers: {
                'content-type': 'application/json'
            },
            params: {
                "tranchId": trancheId,
                "authorityDetails" : authorityDetails
            },
            responseType: 'arraybuffer'
        }
        return this.http(requestString);
    }

    downloadIPANSDLPdf(trancheId) {
        let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL + '/api/downloadIPA&SDL',
            headers: {
                'content-type': 'application/json'
            },
            params: {
                "tranchId": trancheId
            },
            responseType: 'arraybuffer'
        }
        return this.http(requestString);
    }

    downloadInvestorDetails(trancheId){
        let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL + '/api/downloadInvestorDetails',
            headers: {
                'content-type' : 'application/json'
            },
            params: {
                'trancheId': trancheId
            },
            responseType: 'arraybuffer'
        }

        return this.http(requestString);
    }

}

export default SecurityDetailsService;


