import * as appProperties from "../../../config.properties";

class ShowCPService{
    
        constructor($http){
            "ngInject"
            this.http=$http;
            this.name = "showCPService";
        }

        getAllUsers(customerId,pageNumber,pageSize){
            if (customerId == undefined || customerId == null) {
                customerId = "";
            }
             let requestString = {
                 method: 'GET',
                 url: appProperties.CONFIG.BASE_URL+'/api/viewAllCP',
                
                 headers: {
                     'content-type': 'application/json'
                 },
                 params:{
                    pageNumber :pageNumber,
                    pageSize : pageSize,
                    "customerId":customerId
                }
             }
          
             return this.http(requestString);
    
         }

         getCpByCustomerId(customerId,pageNumber, pageSize,){
            console.log(appProperties.CONFIG.BASE_URL+ '/api/getRequestDetails');
            this.isinNumber = ""
            let requestString={
                method:'POST',
                url:appProperties.CONFIG.BASE_URL+'/api/getRequestDetails',
                headers:{
                    'content-type': 'application/json'
                },
                params:{
                    "customerId":customerId,
                    "modelName":"CP",
                    pageNumber: pageNumber,
                    pageSize: pageSize,
                    "isinNumber": this.isinNumber
                }
            }
        return this.http(requestString);
        }
    
    }
    
   
    export default ShowCPService;


