import angular from 'angular';
import uiRouter from 'angular-ui-router';
import reportComponent from './reports.component';
import {default as ReportService} from './reports.service';
require('angucomplete-alt');

let reportModule=angular.module('reports',[
    uiRouter,
    "angucomplete-alt",
])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
    .state('report/reports',{
       // url:'/report/reports',
        //component:'reportComp'
    });

})
.directive('toggle', function(){
    return {
      restrict: 'A',
      link: function(scope, element, attrs){
        if (attrs.toggle=="tooltip"){
          $(element).tooltip();
        }
        if (attrs.toggle=="popover"){
          $(element).popover();
        }
      }
    };
  })


.component('reportComp',reportComponent)
.service('reportService',ReportService)
.name;


export default reportModule;




