import angular from 'angular';
import uiRouter from 'angular-ui-router';
import customerDemoComponent from './demo.component'
import {default as DemoService} from './demo.service';

let customerDemoModule = angular.module('demo', [
    uiRouter,
    
])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
      .state('customer/demo', {
        url: '/customer/demo',
        component: 'demo'
      });
  })

  .component('demo', customerDemoComponent)
  .service('demoService',DemoService)
  .name;


  export default customerDemoModule;
  


