import angular from 'angular';
import uiRouter from 'angular-ui-router';
import benposComponent from './benpos.component';
import manageComponent from './manage/manage.component';
import benposService from './benpos.service';

let benposModule = angular.module('benpos', [
  uiRouter
])

.component('benpos', benposComponent)
.component('benmanage', manageComponent)
.service('benposService', benposService)
.config(($stateProvider, $urlRouterProvider) => {
  "ngInject";

  $stateProvider
    .state({
      name: 'benpos',
      url: '/benpos',
      component: 'benpos'
    }).state({
      name: 'benpos.manage',
      url: '/manage',
      component: 'benmanage'
    })
})

.name;

export default benposModule;



