import angular from 'angular';
import uiRouter from 'angular-ui-router';
import requestComponent from './request.component';
import manageAccComponent from '../manageacc/manageacc.component';
import feesCalculationManagementComponent from '../feescal_management/feescalmanagement.component';
import {default as RequestService} from './request.service';
import manageAccService from '../manageacc/manageacc.service';
import feesCalculationManagementService from '../feescal_management/feescalmanagement.service';
require('angucomplete-alt');

let requestModule=angular.module('request',[
    uiRouter,
    "angucomplete-alt",
])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
    .state('requests/request',{
        url:'/requests/requests',
        component:'requestComp'
    })
    .state('requests/manageacc',{
      url:'/manageacc',
      component:'manageAccComponent'
  })
  .state('requests/feesmanagement',{
    url:'/feesmanagement',
    component:'feesCalculationManagementComponent'
});

})
.directive('toggle', function(){
    return {
      restrict: 'A',
      link: function(scope, element, attrs){
        if (attrs.toggle=="tooltip"){
          $(element).tooltip();
        }
        if (attrs.toggle=="popover"){
          $(element).popover();
        }
      }
    };
  })


.component('requestComp',requestComponent)
.component('manageAccComponent', manageAccComponent)
.component('feesCalculationManagementComponent', feesCalculationManagementComponent)
.service('manageAccService',manageAccService)
.service('requestService',RequestService)
.service('feesCalculationManagementService', feesCalculationManagementService)
.name;


export default requestModule;




