import * as appProperties from "../../../config.properties";
var moment = require('moment');
class FeesCalculationController {

    constructor($state, $scope, $filter, feesCalculationService, commonService, CreditDebitService) {
        "ngInject";


        this.baseUrl = appProperties.CONFIG.BASE_URL;

        this.scope = $scope;
        self = this;
        this.state = $state;
        this.$filter = $filter;
        this.feesCalculation = {};
        this.myStorage = window.localStorage;
        // this.formData=new FormData();
        this.commonService = commonService;
        this.feesCalculationService = feesCalculationService;
        this.CreditDebitService = CreditDebitService;
        this.myStorage = window.localStorage;
        if (this.commonService.rolename != undefined && this.commonService.username) {
            this.myStorage.setItem("role", this.commonService.rolename);
            this.myStorage.setItem("userName", this.commonService.username);
            this.myStorage.setItem("IdList", this.commonService.IdList);
            this.myStorage.setItem("emailId", this.commonService.emailId);
        }
    }

    inputChangedById(item) {
        //console.log('Item '+ JSON.stringify(item));
        //console.log('Item Origi:'+ item.originalObject);
        self.data = item.originalObject;

        self.isUserActive = self.data.isUserActive;
      // debugger;
        if (self.data.status == 'Approved') {
            // console.log('Item is approved');
            self.scope.$broadcast('angucomplete-alt:changeInput', 'designatedAccountNumber', self.data);
            // console.log(self.data.totalFees);
            self.feesCalculation.customerId = self.data.customerId;
            self.feesCalculation.customerName = self.data.customerName;
            self.feesCalculation.designatedAccountNumber = self.data.designatedAccountNumber;
            self.feesCalculation.designatedAccIfsc = self.data.designatedAccIfsc;
            self.feesCalculation.ipaFrequency = self.data.ipaFrequency;
            self.feesCalculation.ipaFees = self.data.ipaFees;
            if(self.data.tds != null){
                self.feesCalculation.tds = self.data.tds;    
                self.feesCalculation.isTDS = self.data.isTDS;
            }
            else{
                self.feesCalculation.tds = 0;
                self.feesCalculation.isTDS = 0;
            }
            self.feesCalculation.totalFees = self.data.totalFees;
            self.feesCalculation.ipaAggrementDate = self.data.ipaAggrementDate;
           
            self.feesCalculation.gst = self.data.gst;

            if (self.feesCalculation.ipaFrequency == 'Dealwise' || self.feesCalculation.ipaFrequency == 'percentFees') {
                console.log('inside ipaFre');
                self.showTrancheId = true;
            }

            if (self.feesCalculation.ipaFrequency == 'percentFees') {
                self.isPercentFees = true;
            }

        } else {
            if (self.data.status == 'Pending' || self.data.status == 'Rejected') {
                alert('Customer is not approved');
                self.data = null;
                return;
            }
        }
    }

    getBalance() {
        $(".se-pre-con").show();
        this.CreditDebitService.getBalance(this.feesCalculation.customerId)
            .then((response) => {
                // console.log(response);
                $(".se-pre-con").hide();
               //==
              //  let tempData = JSON.parse(response.data.serviceResponse);
               // if (response.data.serviceResponse != null) {
                 //   debugger;
                    // console.log("balance Found : "+ response.data.serviceResponse);
                    //    if(response.data.serviceResponse.BalInq_Response.BalInq_Rsp_Body.AcctBal.BalAmt.BalType == 'Available Balance')
                    //          self.feesCalculation.availableBalance = response.data.serviceResponse.BalInq_Response.BalInq_Rsp_Body.AcctBal.BalAmt.amountValue;

                    // let availableBal = tempData["BalInq_Response"]["BalInq_Rsp_Body"]["AcctBal"];
                    // availableBal.forEach(balType => {
                    //     if (balType['BalType'] == 'Available Balance') {
                    //         self.feesCalculation.availableBalance = balType['BalAmt']['amountValue'];
                    //     }
                    // });

                    //==
                    // --
                    var temp = JSON.parse(response.data.serviceResponse);
                    // console.log(temp['BalInq_Response']['BalInq_Rsp_Body']['AcctBal']);
                    let amountData = temp['BalInq_Response']['BalInq_Rsp_Body']['AcctBal'];
                    //console.log("Amount data : "+amountData[1]['BalAmt']['amountValue']);
    
                    if (response.data.serviceResponse != null) {
                        let amountValue = amountData[1]['BalAmt']['amountValue'].toString();
    
                      //  console.log(amountValue);
                        if(amountValue != 0){
                            let firstPartOfAmount = amountValue.substring(0, amountValue.length - 2).concat(".");
                            let lastPartOfAmount = amountValue.substring(amountValue.length - 2);
                            let finalAmount = firstPartOfAmount.concat(lastPartOfAmount);
                           // console.log("Final amount " + finalAmount);
                           self.feesCalculation.availableBalance = finalAmount;   
                        }else{
                            self.feesCalculation.availableBalance = amountValue;
                        }
                    }
                    // --
                    
                //}

            });
    }

    calculateFees(trancheId, customerId) {
        console.log("Tranche ID: " + trancheId + '' + "Customer ID: " + customerId);
        this.feesCalculationService.calculateFees(trancheId, customerId)
            .then((response) => {
                //console.log(JSON.stringify(response));

                if (response.data.serviceStatus == 'SUCCESS') {

                    let data = response.data.serviceResponse;

                    var maturityDate = moment(data.maturityDate, 'DD/MM/YYYY');
                    var valueDate = moment(data.valueDate, 'DD/MM/YYYY');
                    self.tenorInDays = maturityDate.diff(valueDate, 'days');

                    var calFees = (data.totalCPIssue * 0.010 * parseInt(self.tenorInDays) / 365 * 100);
                    var feeWithGST = (calFees * 18) / 100;
                    self.ipaFeeAmount = calFees + feeWithGST;

                    if (data.commercialPaper.customer.ipaFrequency == 'percentFees' && data.commercialPaper.customer.isTDS == true) {
                        var feeWithTDS = (calFees * 10) / 100
                        self.ipaFeeAmount = calFees - feeWithTDS;
                    }

                    self.feesCalculation.ipaFees = parseFloat(self.ipaFeeAmount.toFixed(2));
                } else {
                    alert(response.serviceResponse.status);
                }

            });
    }

    save() {
      //   debugger;
        //console.log("Form data : "+ data.ipaAggrementDate);
        if(!self.isUserActive){
            alert("Customer status is Inactive");
            return;
        }

        if(self.feesCalculation.tds != 0){
            alert(" TDS applicable - Fees collection not allowed.");
            return;
        }

        if(self.feesCalculation.availableBalance == null){
            alert("Please check available balance");
            return;
        }

        if(self.feesCalculation.ipaFrequency == 'Dealwise' || self.feesCalculation.ipaFrequency == 'percentFees'){
            if(self.feesCalculation.trancheId == null){
                alert("Tranche Id required");
                return;
            }
        }
        

        $(".se-pre-con").show();
        this.feesCalculation.createdBy = this.myStorage.getItem("userName");
        //self.formData.append('data',angular.toJson(data));
        this.feesCalculationService.saveFeesDetails(this.feesCalculation)
            .then((response) => {
                //debugger;
                $(".se-pre-con").hide();
                if (response.data.serviceResponse == 'SUCCESS') {
                    self.submitResponse = true;
                    self.message = response.data.serviceStatus;
                    //console.log('Success')  
                    //   self.feesCalculation.customerId = null;
                    self.feesCalculation = {};
                } else {
                    //console.log('Fail :'+  response.data.serviceError)
                    self.submitResponse = false;
                    self.message = response.data.serviceStatus;
                }
            });
    }

}
FeesCalculationController.$inject = ['$state', '$scope', '$filter', 'feesCalculationService', 'commonService', 'CreditDebitService']
export default FeesCalculationController;


