import angular from 'angular';
import uiRouter from 'angular-ui-router';
import showBrDetailsComponent from './show.component'
import {default as ShowBrService} from './show.service';

let showBrDetailsModule = angular.module('show', [
    uiRouter,
    
])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
      .state('boardResolution/show', {
        url: '/boardResolution/show',
        component: 'showBr'
      });
  })
  .directive('toggle', function(){
    return {
      restrict: 'A',
      link: function(scope, element, attrs){
        if (attrs.toggle=="tooltip"){
          $(element).tooltip();
        }
        if (attrs.toggle=="popover"){
          $(element).popover();
        }
      }
    };
  })
  
  .component('showBr', showBrDetailsComponent)
  .service('showBrService',ShowBrService)
  .name;


  export default showBrDetailsModule;


