import * as appProperties from "../../../config.properties";
var self;
var moment = require('moment');
class brDetailsController {

    brDoc;
    bbDoc;
    mailId = [];
    formdata = new FormData();
    isUserActive;
    form = {
        customerId: null,
        customerName: null,
        approvedAmount: null,

        // 08-01-19
        buyBackLimit: null,
        resolutionDate: null,
        buyBackDate: null,
        customer: null,
        createdBy: null,
        updatedBy: null,
        status: null,

        mailIdList: [],
        makerMailId: null,

        buybackDoc: null,
        brDoc: null

    }

    constructor(brService, $stateParams, commonService, $state, $scope,$filter) {
        
        this.baseUrl = appProperties.CONFIG.BASE_URL;
        this.name = "br_details";
        this.brService = brService;
        self = this;
        this.scope = $scope;
        this.state = $state;
        this.$filter = $filter;
        this.commonService = commonService;
        this.myStorage = window.localStorage;
        
        if (this.commonService.rolename != undefined && this.commonService.username) {

            this.myStorage.setItem("role", this.commonService.rolename);
            this.myStorage.setItem("userName", this.commonService.username);
            this.myStorage.setItem("IdList", this.commonService.IdList);
            this.myStorage.setItem("emailId", this.commonService.emailId);
        }
        this.mailId.push(this.myStorage.getItem("IdList"));
        this.role = this.myStorage.getItem("role");
        if ($stateParams.br) {
            this.isEdit = true;
            this.isView = true;
            this.customerId = $stateParams.br;
            this.form = $stateParams.br;
            this.boardDoc = $stateParams.br.brDoc;
            this.buybackDoc = $stateParams.br.buybackDoc; // here
        }
    }


    inputChangedByName(item) {
        if (item) {
            self.data = item.originalObject;
        }
        self.scope.$broadcast('angucomplete-alt:changeInput', 'Customers', self.data.customerId);
    }

    inputChangedById(item) {
        if (item) {
            self.data = item.originalObject;
        }
        self.scope.$broadcast('angucomplete-alt:changeInput', 'customername', self.data.customerName);
        if(self.data.customer){
            self.isUserActive = self.data.customer.isUserActive;
        }else{
            self.isUserActive = self.data.isUserActive;
        }
        
    }

    validateResolutionDate(){
     
        var currentDate=moment(moment().format('DD/MM/YYYY'), 'DD/MM/YYYY');
        var resoDate=moment(this.form.resolutionDate, 'DD/MM/YYYY');
        var isValid=moment(resoDate).isAfter(currentDate);
        if(isValid){
            alert("Resolution Date must be Before of Current date.");
            this.form.resolutionDate=null;
            return;
        }

    }

    checkFileSize(doc) {
        var mb = 1024 * 1024;
        var sizeInMB = doc[0].size / mb;
        if (sizeInMB > 10) {
            alert("File size should not greater than 10 MB")
            document.getElementById("boardDoc").value = null;
        }
    }

    back() {
        this.state.go('requests/request');
    }

    cancelled() {
        this.state.go('boardResolution/show');
    }

    addBRDetails() {

        if(!self.isUserActive){
            alert("Customer status is Inactive");
            return;
        }

        this.form.customerId = this.data.customerId;
        this.form.customerName = this.data.customerName;
        this.form.createdBy = this.myStorage.getItem("userName");
        this.form.mailIdList = this.mailId;
        this.form.makerMailId = this.myStorage.getItem("emailId");
        this.form.status = 'Pending';

        if(this.data.status=='Pending' || this.data.status=='CallBack'){
            alert("Customer is not Approved For BR...!");
            return;
        }
        if (typeof this.form.approvedAmount != 'number') {
            if (this.form.approvedAmount.includes(",")) {
                var mybramt = parseInt(this.form.approvedAmount.replace(/,/g, ''));
                this.form.approvedAmount=mybramt;
            }
        }
        //var mybramt=parseInt(this.form.approvedAmount.replace(/,/g,''));
        //this.form.approvedAmount=mybramt;
        
        if(this.form.approvedAmount % 500000 != 0){
              alert("Approved Amount amount should be multiple of 5 Lakh");
              return;
        }

        if(this.form.buyBackLimit % 500000 != 0){
            alert("Buy back amount should be multiple of 5 Lakh");
            return;
        }
        if (this.brDoc != null && this.brDoc.length > 0) {

            this.form.brDoc=this.brDoc[0].name;
            var reg = /(.*?)\.(pdf|PDF|doc|docx|xls|xlsx)$/;
            this.formdata.append('uploadingFiles', this.brDoc[0]);  

                     // this.formdata.append('requestInput', angular.toJson(this.form));
                       // cut from here .. addBrService()
        } else {
            alert("Document not choose...Please choose Document...!");
            return;
        }

        if(this.buybackDoc != null && this.buybackDoc.length > 0){
            this.form.buybackDoc = this.buybackDoc[0].name;
            var reg = /(.*?)\.(pdf|PDF|doc|docx|xls|xlsx)$/;
            this.formdata.append('uploadingFiles', this.buybackDoc[0]);
           
         } //else{
        //     alert(" here Document not choose...Please choose Document...!");
        //     return;
        // }
        //console.log(this.formdata);
        this.formdata.append('requestInput', angular.toJson(this.form));
        this.brService.addBRDetails(this.formdata).then((response) => {
            $(".se-pre-con").hide();
            if (response.data.serviceResponse == 'SUCCESS' && response.data.serviceStatus == "200") {
                alert("Br Successfully Added ...!");
                this.state.go('boardResolution/show');
            }
            else if (response.data.serviceStatus == "SUCCESS" && response.data.serviceError == 'Unable to send email') {
                alert("BR Successfully Added but Mail has not Send...!");
                this.state.go('boardResolution/show');
            }
            else {
                alert("Error- Something went wrong...!");
                return;
            }
        });

    }


    updateBRDetails() {
        //debugger;

        if(!self.isUserActive){
            alert("Customer status is Inactive");
            return;
        }
        delete this.form.customer;
        this.form.updatedBy = this.myStorage.getItem("userName");
        this.form.mailIdList = this.mailId;
        this.form.makerMailId = this.myStorage.getItem("emailId");
        var mybramt=parseInt(this.form.approvedAmount.replace(/,/g,''));
        this.form.approvedAmount=mybramt;

        var reg = /(.*?)\.(pdf|PDF|doc|docx|xls|xlsx)$/;

        if(this.form.approvedAmount % 500000 != 0){
            alert("Approved Amt should be in multiple of 5 Lakh");
            return;
        }
        // console.log("Old board doc: "+this.boardDoc);
        // console.log("Old buyback doc: "+ this.buybackDoc);
        if (this.brDoc != null && this.brDoc.length > 0) {

            var file = this.brDoc[0].name;
            this.form.brDoc=this.brDoc[0].name;
           
            this.formdata.append('uploadingFiles', this.brDoc[0]);

        } else {
            alert("Please choose Document...!");
            // console.log("br doc: "+this.form.brDoc);
            this.form.brDoc = this.boardDoc;
            this.formdata.append('uploadingFiles', this.boardDoc);
            return;
        }

        if (this.buybackDoc != null && this.buybackDoc.length > 0) {
            var file = this.buybackDoc[0].name;
            this.form.buybackDoc=this.buybackDoc[0].name;
            
            this.formdata.append('uploadingFiles', this.buybackDoc[0]);
        }else{
            // if(this.buyBackLimit != null){
            //     if(this.buybackDoc == null || this.buybackDoc == ""){

            //     }
            // }
           // alert("Please choose Document...!");
            console.log("bb doc"+ this.buybackDoc);
            this.form.buybackDoc = this.buybackDoc;
            this.formdata.append('uploadingFiles', this.buybackDoc);
          //  return;
        }

        this.formdata.append('requestInput',angular.toJson(this.form))
            //if (file.match(reg)) {
                        this.brService.updateBRDetails(this.formdata).then((response) => {
                            $(".se-pre-con").hide();
                            if (response.data.serviceResponse == 'SUCCESS') {
                                alert("BR Succesfully Updated...!");
                                this.state.go('boardResolution/show');
                            }
                            else if (response.data.serviceError == 'Unable to send email') {
                                alert("BR Successfully Updated but Mail has not Send...!");
                                this.state.go('boardResolution/show');
                            }
                            else {
                                alert("Error- Something went 00h0fghfghwrong...!");
                                return;
                            }
                        })
            //} else {
              //  alert("Supported only PDF,DOC,Excel file...!");
                //return;
           // }
    }

    uploadBRDocument() {
        var trancheId;
        var dealNumber;
        return this.brService.uploadBRDocument(this.brDoc, trancheId, dealNumber, this.form.customerId).then((response) => {
            return response.data;
        });
    }

    downloadBRDocument() {
        var fileName = this.boardDoc;
        
        var extension=fileName.substr((fileName.lastIndexOf('.') + 1));

        this.brService.downloadDocument(fileName, this.form.customerId).then((response) => {
            var blob = response;
            var binaryData = [];
            binaryData.push(response.binaryData);
            let a = document.createElement("a");
            a.style = "display: none";
            document.body.appendChild(a);

            var url = window.URL.createObjectURL(new Blob([response.data], { type: "application/extension" }))
            a.href = url;
            a.download = 'BR_DOC.'+extension;
            a.click();
            window.URL.revokeObjectURL(url);
        })

    }

    downloadBBDocument() {
        var fileName = this.buybackDoc;
        
        var extension=fileName.substr((fileName.lastIndexOf('.') + 1));

        this.brService.downloadDocument(fileName, this.form.customerId).then((response) => {
            var blob = response;
            var binaryData = [];
            binaryData.push(response.binaryData);
            let a = document.createElement("a");
            a.style = "display: none";
            document.body.appendChild(a);

            var url = window.URL.createObjectURL(new Blob([response.data], { type: "application/extension" }))
            a.href = url;
            a.download = 'BB_DOC.'+extension;
            a.click();
            window.URL.revokeObjectURL(url);
        })

    }

}

brDetailsController.$inject = ['brService', '$stateParams', 'commonService', '$state', '$scope','$filter'];
export default brDetailsController;


