class ShowCPDetailsController{
    
    pageNumber = 1;
	pageSize = 10;
    masterArray = [];
    arrayToDisplay=[];
    
    customerId;
    searchArray=[];
    initialArray=[];


    constructor(showCPService,$state,commonService){
            this.name="cp_Details";
            this.state=$state;
            this.showCPService="showCPService";
            this.showCPService=showCPService;
            this.commonService = commonService;

            this.getAllUsers();
               
    }

    fetchCpRequest() {
        $(".se-pre-con").show();
        if( this.customerId != undefined && this.customerId != ""){
        if(this.customerId.length>=3){
    this.showCPService.getCpByCustomerId(this.customerId,this.pageNumber,this.pageSize).then((response)=>{
        $(".se-pre-con").hide();
        this.disable=false;
        if(response.data.serviceResponse!=null && response.data.serviceStatus == "SUCCESS"){

            this.pgNo=this.pageNumber;
            this.pgSize=this.pageSize;
           this.searchArray = response.data.serviceResponse.cpList;  
           this.arrayToDisplay=this.searchArray 
        }else if(response.data.serviceError=='No Record found'){
            this.disable=true;
           
        }else{
           
        }
     });
    }
    }
    else{
       // $(".se-pre-con").hide();
       $(".se-pre-con").hide();
        this.arrayToDisplay=this.initialArray;
        
    }
}

    getAllUsers(){

        $(".se-pre-con").show();
        this.showCPService.getAllUsers(this.customerId,this.pageNumber,this.pageSize).then((response)=>{
            this.disable=false;

            if(response.data.serviceResponse!=null && response.data.serviceStatus == "SUCCESS"){
                $(".se-pre-con").hide();
                this.pgNo=this.pageNumber;
                this.pgSize=this.pageSize;

                this.initialArray = response.data.serviceResponse.cpList;  
                this.arrayToDisplay=this.initialArray;
                
    
            }else if(response.data.serviceError=='No Record found'){
               
                this.disable=true;
                $(".se-pre-con").hide();

            }else{
                alert("Internal server error")
                console.log("Something went wrong");
                $(".se-pre-con").hide();
            }

        })
        .catch(err => {
            console.log('mainerror', err);
            $(".se-pre-con").hide();
        });

    }

    numberOfPages(){
		
        return Math.ceil(this.masterArray.length/this.pageSize);                
    }

    jumpToFirst(){
        this.pageNumber=1;
        this.getAllUsers()
    }

    previous(){
        this.pageNumber--;
        this.getAllUsers()
    }

    next(){
        this.pageNumber++;
        this.getAllUsers()
    }

    editCP(cplist,$state){
        
        // if(cplist.status=='CallBack' || cplist.status=='Approved'){
        //     this.state.go("cpReg/editCP",{ 'cplist':cplist });
        // }else{
        //     alert("You can't update data ...data is not approved or callbacked");
        // }
        this.state.go("cpReg/editCP",{ 'cplist':cplist });
        
    }

}
    
    
   ShowCPDetailsController.$inject =['showCPService','$state','commonService'];
   
   export default ShowCPDetailsController;


