import * as appProperties from "./config.properties";
class ServiceInterceptor {
   
    constructor(commonStorage) {

        window.commonStore = commonStorage;
        this.userCookies = {};
        this.baseUser_logouturl = appProperties.CONFIG.Base_user_logouturl;

        let local = {};
        
        var splittedCookie = document.cookie.split(";", 5);
        splittedCookie.forEach(function (element) {
            var splittedElement = element.split("=", 2);
           
            local[splittedElement[0].trim()] = splittedElement[1];
        });
        this.userCookies = local;
       
       // var decodedCookie = decodeURIComponent(document.cookie);
        //var ca = decodedCookie.split(';');
        // function getCookie(cname) {
        //     var name = cname + "=";
        //     var decodedCookie = decodeURIComponent(document.cookie);
        //     var ca = decodedCookie.split(';');
        //     for (var i = 0; i < ca.length; i++) {
        //         var c = ca[i];
        //         while (c.charAt(0) == ' ') {
        //             c = c.substring(1);
        //         }
        //         if (c.indexOf(name) == 0) {
        //             return c.substring(name.length, c.length);
        //         }
        //     }
        //     return "";
        // }
    }

    // responseError(res) {
    //     if (res.status == 401 || res.error == "Unauthorized" || res.status == 500)  {
    //         document.cookie = 'access-token=; Path=/';
    //        // var logouturl = appProperties.CONFIG.Base_user_logouturl;
    //         window.location.href =  this.baseUser_logouturl;
    //         /*window.location.href = '/umgmt/';			
    //             setTimeout(function(){
    //           }, 2000); */
    //     }
    //     return res;
    // }


    // aziz part
    responseError(res) {
        if (res.status == 401 || res.error == "Unauthorized"  
            //|| res.status == 405 || res.status == 500 !!!! fix in sec controller at backend
      
        ) {
            document.cookie='access-token=; Path=/';
              /**/ //window.location.href = '/umgmt/';			
              //window.location.href =  this.baseUser_logouturl;
              window.location.href = appProperties.CONFIG.Base_user_logouturl;
                 setTimeout(function(){
               }, 2000);
               
           }
        return res;
    }


    response(res) {
        return res;
    }
    request(config) {
        if (window.commonStore.userCookies["access-token"] == undefined || window.commonStore.userCookies["access-token"] == '') {
           // or document.cookie["access-token"] to get the token
            //window.location.href =  this.baseUser_logouturl;
            window.location.href =  appProperties.CONFIG.Base_user_logouturl;
        }
        config.headers['Authorization'] = 'Token ' + window.commonStore.userCookies["access-token"];
        //config.headers['content-type'] = 'application/json';
        return config;
    }

    requestError(config) {
        return config;
    }
}
ServiceInterceptor.$inject = ['commonStorage'];
export default ServiceInterceptor;


