import angular from 'angular';
import uiRouter from 'angular-ui-router';
import securityDetailsComponent from './details.component'
import {default as SecurityDetailsService} from './details.service';

let securityDetailsModule = angular.module('details', [
    uiRouter,
    
])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
      .state('security/details', {
        url: '/security/details',
        component: 'secdetails'
      });
  })
  .directive('toggle', function(){
    return {
      restrict: 'A',
      link: function(scope, element, attrs){
        if (attrs.toggle=="tooltip"){
          $(element).tooltip();
        }
        if (attrs.toggle=="popover"){
          $(element).popover();
        }
      }
    };
  })

  .component('secdetails', securityDetailsComponent)
  .service('securityDetailsService',SecurityDetailsService)
  .name;


  export default securityDetailsModule;
  


