import * as appProperties from "../../../config.properties";

class feesCalculationManagementController {

    pagenumber = 1;
    pageSize = 10;
    masterArray = [];
    arrayToDisplay;

    constructor(feesCalculationManagementService, $state, commonService) {
        "ngInject"
        
        this.baseUrl = appProperties.CONFIG.BASE_URL;
        this.state = $state;
        this.feesCalculationManagementService = feesCalculationManagementService;

        this.commonService = commonService;
        this.role = this.commonService.rolename;
        this.userName = this.commonService.username;
        this.IdList = this.commonService.IdList;

        this.myStorage = window.localStorage;
        if (this.commonService.rolename != undefined && this.commonService.username) {

            this.myStorage.setItem("role", this.commonService.rolename);
            this.myStorage.setItem("userName", this.commonService.username);
            this.myStorage.setItem("IdList", this.commonService.IdList);
        }

        this.getAllRequest();

    }

    getAllRequest() {
        
        this.feesCalculationManagementService.getAllRequest(this.pagenumber, this.pageSize).then((response) => {
           
            this.disable = false;
            console.log("Data found: "+response.data.serviceResponse);

            if (response.data.serviceResponse != null && response.data.serviceStatus == "SUCCESS") {

                this.pgNo = this.pagenumber;
                this.pgSize = this.pageSize;

               // console.log('Data to display : '+ response.data.serviceResponse);
                this.arrayToDisplay = response.data.serviceResponse;

            } else if (response.data.serviceError == 'No Record found') {
                //console.log('No data found');
                this.disable = true;
               

            } else {
               
            }
        })
    }

    approveRequest(request) {
        $(".se-pre-con").show();
      //  request.createdBy = this.myStorage.getItem('userName');
        this.feesCalculationManagementService.approveRequest(request).then((response) => {
            
            $(".se-pre-con").hide();
            if (response.data.serviceStatus == 'SUCCESS') {
                // window.location.reload();
                // alert('Request Approved for '+request.id);
                alert(response.data.serviceResponse);
            }else{
                alert("Error: "+response.data.serviceError);
            }
            this.getAllRequest();
        })

    }


    rejectRequest(request) {

        var confirm = prompt('Enter comment for Reject Request');
        

        if (confirm == null) {
            return;
        }

        if (confirm == '') {
            alert("Comment is required for Reject Request...!");
            return;
        }
       
        // var status = 'Rejected';
        // request.checkerId = this.myStorage.getItem("userName");
        // request.status = status;
        request.comments = confirm;

        this.feesCalculationManagementService.rejectRequest(request).then((response) => {
            
            $(".se-pre-con").hide();
            if (response.data.serviceStatus == 'SUCCESS') {
                // window.location.reload();
                alert('Request rejected for '+request.id);
            }else{
                alert("Error :"+response.data.serviceError);
            }
            this.getAllRequest();
        })
    }
    
    numberOfPages() {
        return Math.ceil(this.masterArray.length / this.pageSize);
    }

    jumpToFirst() {
        this.pagenumber = 1;
        this.getAllRequest()
    }

    previous() {
        this.pagenumber--;
        this.getAllRequest()
    }

    next() {
        this.pagenumber++;
        this.getAllRequest()
    }

}
//FeesCalculationManagementController.$inject = ['FeesCalculationManagementService', '$state', 'commonService'];
export default feesCalculationManagementController;


