import commonService from "./common.service";
import * as appProperties from "./config.properties";

class AppController {

    username;
    lastTokenGenerated;

    constructor(appService, commonStorage, commonService) {
        this.name = "appController";
        this.appService = appService;
        this.commonService = commonService;

        this.myStorage = window.localStorage;

        this.commonService.getUserById().then((response) => {
            if (response != undefined) {
                this.myStorage.setItem("role", response);
                this.role = this.myStorage.getItem("role")
            }

        })

        if (this.myStorage.getItem("role") == 'ipa_maker') {
            this.userRole = 'ipa_checker';
            this.commonService.getAllCheckersDetails(this.userRole);
        }




    }

    SignOut() {
        
        document.cookie = 'access-token=; Path=/;'
        document.cookie = 'loggedId=; Path=/;'
        var logouturl = appProperties.CONFIG.Base_user_logouturl
        window.location.href = logouturl;
    }

    clickhere() {
        
    }
    data = "controller injected !";
    
}
AppController.$inject = ['appService', 'commonStorage', 'commonService'];

export default AppController;


