import * as appProperties from "../../../config.properties";

class CreateService {

    constructor($http) {
        "ngInject"
        this.name = "createService";
        this.http = $http;

    }



    createCustomer(form) {

        $(".se-pre-con").show();
        let requestString = {
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/registerCustomer',
            headers: {
                'content-type': undefined
            },
            data: form

        }
        return this.http(requestString);
    }


    UpdateCustomer(form) {

        $(".se-pre-con").show();
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/updateCustomer',
            headers: {
                'content-type': undefined
            },

            data: form

        })

    }


    uploadDocument(ipaAggrementDoc, emailFaxEndemnityDoc, trancheId, dealNumber, customerId) {
       
        
        
        var payload = new FormData();

        if(ipaAggrementDoc!=null){
            payload.append('ipaAggrementDoc', ipaAggrementDoc[0]);
        }
        if(emailFaxEndemnityDoc!=null){
            payload.append('emailFaxEndemnityDoc', emailFaxEndemnityDoc[0]);
        }

        payload.append('trancheId', trancheId);
        payload.append('dealNumber', dealNumber);   
        payload.append('customerId', customerId);

        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/uploadDocument',
            processData: false,
            headers: {
                'content-type': undefined
            },
            data: payload
        })

    }

    downloadDoc(fileName, customerId) {

        return this.http({
            method: 'Post',
            url: appProperties.CONFIG.BASE_URL + '/api/downloadDoc',
            processData: false,
            headers: {
                'content-type': 'application/json'
            },
            params: {
                "fileName": fileName,
                "customerId": customerId
            },
            responseType: 'blob'

        })

    }



}

export default CreateService;


