import * as appProperties from "../../config.properties";
class CreditDebitService {
    constructor($http) {
        "ngInject"
        this.name = "CreditDebitService";
        this.$http = $http;
    }

    saveData(data) {
        return this.$http({
            method: "POST",
            url: appProperties.CONFIG.BASE_URL+"/api/account_master/create",
            headers:{
                "content-type": undefined
            },
            data: data
            });        
    }

    getBalance(custId){
        return this.$http({
            method: "GET",
            url : appProperties.CONFIG.BASE_URL+ "/api/account_master/BalanceEnquiry",
            headers:{
                "content-type": undefined
            },
            params :{
               'custId' : custId
            }
        });
    }
}

export default CreditDebitService;


