import * as appProperties from "../../../config.properties";

class AddSecurityService {

    constructor($http) {
        "ngInject"

        this.name = 'securityService';
        this.http = $http;
    }

    getIssuerCategory() {

        let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL + '/api/getIssuerCategory',
            headers: {
                'content-type': 'application/json'
            },
        }
        return this.http(requestString);

    }

    getIndustrySector() {

        let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL + '/api/getIndustryType',
            headers: {
                'content-type': 'application/json'
            },
        }
        return this.http(requestString);
    }

    onMaturityDateChanged(customerId, maturityDate) {
        console.log('inside onMaturityDateChanged Service');

        let postData = {
            "customerId": customerId,
            "maturityDate": maturityDate
        }

        console.log(postData);
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/tranche/getIsinNumberOnCustomerIdAndMaturityDate',
            headers: {
                'content-type': 'application/json'
            },
            data: postData
        });

    }

    registerTranche(form, remainingAmount) {
        $(".se-pre-con").show();
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/registerTranch',
            headers: {
                //'content-type': 'application/json'
                'content-type': undefined
            },

            data: form,
            params: {
                'previousAmt': remainingAmount
            }
        })

    }

    updateTranche(form, remainingAmount) {

        $(".se-pre-con").show();
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/updateTranch',
            headers: {
                // 'content-type': 'application/json'
                'content-type': undefined
            },
            data: form,
            params: {
                'previousAmt': remainingAmount
            }


        })

    }

    uploadDoc(coveringLetterDoc, rbiLetterDoc, offerLetterDoc, isinDoc, trancheId, dealNumber, customerId) {

        var payload = new FormData();
        if (coveringLetterDoc != null) {
            payload.append('coveringLetterDoc', coveringLetterDoc[0]);
        }
        if (rbiLetterDoc != null) {
            payload.append('rbiLetterDoc', rbiLetterDoc[0]);
        }
        if (offerLetterDoc != null) {
            payload.append('offerLetterDoc', offerLetterDoc[0]);
        }
        if (isinDoc != null) {
            payload.append('isinDoc', isinDoc[0]);
        }

        payload.append('trancheId', trancheId);
        payload.append('dealNumber', dealNumber);
        payload.append('customerId', customerId)


        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/uploadDocument',
            processData: false,
            headers: {
                'content-type': undefined
            },
            data: payload
        })

    }


    uploadDoc1(type, doc, trancheId, dealNumber, customerId) {

        var payload = new FormData();

        if (type == "coveringLetterDoc") {
            var coveringLetterDoc = doc;
            payload.append('coveringLetterDoc', coveringLetterDoc[0]);

        }
        if (type == "rbiLetterDoc") {
            var rbiLetterDoc = doc;
            payload.append('rbiLetterDoc', rbiLetterDoc[0]);
        }
        if (type == "offerLetterDoc") {
            var offerLetterDoc = doc;
            payload.append('offerLetterDoc', offerLetterDoc[0]);
        }
        if (type == "isinDoc") {
            var isinDoc = doc;
            payload.append('isinDoc', isinDoc[0]);
        }
        if (type == "defaulterDoc") {
            var defaulterDoc = doc;
            payload.append('defaulterDoc', defaulterDoc[0]);
        }


        payload.append('trancheId', trancheId);
        payload.append('dealNumber', dealNumber);
        payload.append('customerId', customerId)


        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/uploadDocument',
            processData: false,
            headers: {
                'content-type': undefined
            },
            data: payload
        })

    }

    downloadDocument(fileName, customerId, trancheId) {

        return this.http({
            method: 'Post',
            url: appProperties.CONFIG.BASE_URL + '/api/downloadTrancheDoc',
            processData: false,
            headers: {
                'content-type': 'application/json'
            },
            params: {
                "fileName": fileName,
                "customerId": customerId,
                "trancheId": trancheId
            },
            responseType: 'blob'

        })

    }


    sumOfTranche(valueDate, cpNumber) {

        return this.http({
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL + '/api/sumoftranch',
            processData: false,
            headers: {
                'content-type': 'application/json'
            },
            params: {
                "valueDate": valueDate,
                "cpNumber": cpNumber
            }

        })

    }

    getRatingsDetailsByCpAndTrancheId(cpId, trancheId) {
        // debugger;
        $(".se-pre-con").show();
        var trancheDetails = {
            "cpId": cpId.id,
            "trancheId": trancheId.id
        }
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/getRatingsDetailsByCPAndTrancheId',
            headers: {
                'content-type': 'application/json'
                //'content-type': undefined
            },

            params: trancheDetails

        });

    }

    getCpIdUsingTrancheId(tranche) {
        // console.log("Found : " + tranche[0]['id']);
        var trancheId = tranche[0]['id'];
        $(".se-pre-con").show();
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/getCpIdUsingTrancheId',
            headers: {
                // 'content-type': 'application/json'
                 'content-type': undefined
            },
            params: {
                'trancheId': trancheId
            }
            
        });
       
    }

    getRatingDetailsByCp(cp) {
        $(".se-pre-con").show();
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/getRatingDetailsByCp',
            headers: {
                'content-type': 'application/json'
            },
            params: cp.id
        });
    }

    getOriginalRatingsDetailsByCpId(customerId) {
        $(".se-pre-con").show();
        var custId = {
            "custId": customerId
        }

        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/getCpDetailsUsingCustomerId',
            headers: {
                // 'content-type': 'application/json'
                'content-type': undefined
            },

            params: custId

        });
    }
}

export default AddSecurityService;


