import * as appProperties from "../../config.properties";

class CreditDebitController {



    constructor(CreditDebitService, $stateParams, commonService, $state, $scope, $filter) {
        this.baseUrl = appProperties.CONFIG.BASE_URL;
        this.CreditDebitService = CreditDebitService;
        this.scope = $scope;
        self = this;
        this.state = $state;
        this.$filter = $filter;
        this.commonService = commonService;
        this.myStorage = window.localStorage;
        this.formData = new FormData();
        this.balanceManager = {};
        this.$stateParams = $stateParams;
        //   if ($stateParams.br) {
        //     this.isEdit = true;
        //     this.isView = true;
        //     this.balanceManager = $stateParams;
        //     this.customerId = $stateParams.creditdebit;
        //     this.form = $stateParams.creditdebit;
        // }
        this.myStorage = window.localStorage;
        if (this.commonService.rolename != undefined && this.commonService.username) {
            this.myStorage.setItem("role", this.commonService.rolename);
            this.myStorage.setItem("userName", this.commonService.username);
            this.myStorage.setItem("IdList", this.commonService.IdList);
            this.myStorage.setItem("emailId", this.commonService.emailId);
        }
    }

    inputChangedById(item) {
        if (item) {
            self.data = item.originalObject;
           // console.log("Data " + item.originalObject.status);
           // console.log(self.data);
           self.isUserActive = self.data.isUserActive;

            if (self.data.status == 'Approved') {
                self.scope.$broadcast('angucomplete-alt:changeInput', 'designatedAccountNumber', self.data);
                //this.designatedAccountNumber = self.data.designatedAccountNumber;
                // self.form.accountNumber = self.data.accountNumber;
                //self.accountNumber = self.data.accountNumber;
                self.balanceManager.customerId = self.data.customerId;
                self.balanceManager.customerName = self.data.customerName;
                self.balanceManager.designatedAccountNumber = self.data.designatedAccountNumber;
                self.balanceManager.designatedAccIfsc = self.data.designatedAccIfsc;
                self.balanceManager.axisAccountNumber = self.data.accountNumber;
                self.balanceManager.axisIfscCode = self.data.axisIfscCode;
                self.balanceManager.otherBankAccount = self.data.otherBankAccount;
                self.balanceManager.ifscCode = self.data.ifscCode;
                // To map with Other Bank IFSC as Customer stores Other Bank IFSC as IFSC 
                self.balanceManager.otherBankIfscCode = self.data.ifscCode;
                self.balanceManager.bankName = self.data.bankName;
                self.balanceManager.amount = self.data.amount;
                //console.log("Desig Acc IFSC: " + self.balanceManager.designatedAccIfsc);
                self.balanceManager.axisAccBranchCode = self.data.axisAccBranchCode;
                self.balanceManager.desigAccBranchCode = self.data.desigAccBranchCode;
            }
            else {
                if (self.data.status == 'Pending' || self.data.status == 'Rejected') {
                    alert('Customer is not Approved/Pending');
                    self.data = null;
                    return;
                }

            }

            // this.form.accountNumber  = self.data.accountNumber;
            //console.log(this.form.accountNumber);
        }
    }


    getBalance() {
        console.log("in show bal");
        $(".se-pre-con").show();
        this.CreditDebitService.getBalance(this.balanceManager.customerId)
            .then((response) => {
                $(".se-pre-con").hide();
                var temp = JSON.parse(response.data.serviceResponse);
                // console.log(temp['BalInq_Response']['BalInq_Rsp_Body']['AcctBal']);
                let amountData = temp['BalInq_Response']['BalInq_Rsp_Body']['AcctBal'];
                //console.log("Amount data : "+amountData[1]['BalAmt']['amountValue']);

                if (response.data.serviceResponse != null) {
                    let amountValue = amountData[1]['BalAmt']['amountValue'].toString();

                  //  console.log(amountValue);
                    if(amountValue != 0){
                        let firstPartOfAmount = amountValue.substring(0, amountValue.length - 2).concat(".");
                        let lastPartOfAmount = amountValue.substring(amountValue.length - 2);
                        let finalAmount = firstPartOfAmount.concat(lastPartOfAmount);
                       // console.log("Final amount " + finalAmount);
                        self.balanceManager.availableBalance = finalAmount;   
                    }else{
                        self.balanceManager.availableBalance = amountValue;
                    }
                }
            });

    }

    submit(data) {
//        debugger;
        // console.log('Inside Submit'+ data);
        //console.log('Form Data ', data.$$controls[0].$viewValue);

        // self.balanceManager.customerId = self.data.customerId;
        // self.balanceManager.designatedAccountNumber = self.data.designatedAccountNumber;
        // self.balanceManager.accountNumber = self.data.accountNumber;
        // self.balanceManager.axisIfscCode = self.data.axisIfscCode;
        // self.balanceManager.otherBankAccount = self.data.otherBankAccount;
        // self.balanceManager.ifscCode = self.data.ifscCode;
        // self.balanceManager.bankName = self.data.bankName;
        // self.balanceManager.amount = self.data.amount;

        //console.log("Balance Man: "+self.balanceManager);
        if(!self.isUserActive){
            alert("Customer status is Inactive");
            return;
        }

        if( self.balanceManager.availableBalance == null){
            alert("Please check available balance");
            return;
        }

        if (self.balanceManager.availableBalance === 0) {
            alert("No balance is available");
            return;
        }
        
        let enteredAmount = parseFloat(self.balanceManager.amount);
        let availableBalance = parseFloat(self.balanceManager.availableBalance);

        if (enteredAmount > availableBalance) {
            alert("Entered amount should be less or equal to available balance");
            return;
        }

        if(self.balanceManager.amount < 200000 &&  self.balanceManager.bankSelected == 'rtgs'){
            alert("Please enter amount greater than 2 Lakhs");
            return;
        }
        
        $(".se-pre-con").show();
        self.balanceManager.createdBy = this.myStorage.getItem("userName");
        this.formData.append('accmaster_req_data', angular.toJson(self.balanceManager));
        //console.log("form data found : "+this.formData);
        this.CreditDebitService.saveData(this.formData)
            .then(function (response) {
                $(".se-pre-con").hide();
                console.log(response);
                if (response.data.serviceResponse == 'SUCCESS') {
                    self.submitResponse = true;
                    self.message = "Data saved successfully";
                    //console.log('Success')  
                    self.balanceManager = {};
                } else {
                    //console.log('Fail :'+  response.data.serviceError)
                    self.submitResponse = false;
                    self.message = response.data.serviceError;
                }
            });




    }

    //this.balanceManager.customerId = data[0].$$controls[0].$viewValue;
    //console.log(self.data);
    //console.log("Form Data: Cust Id:" + self.balanceManager.customerId + " ,Desig Acc: " + self.balanceManager.designatedAccountNumber +",Amount: "+ data.amount + " ,Axis Acc"+ self.data.accountNumber + " , Axis IFSC"+data.axisIfscCode + " , Other Acc: "+ data.otherBankAccount + ", other IFSC:"+ data.ifscCode+ ", Bank Name: "+ data.bankName );
}

CreditDebitController.$inject = ['CreditDebitService', '$stateParams', 'commonService', '$state', '$scope', '$filter'];
export default CreditDebitController;


