import * as properties from '../../config.properties'

export default class BuyBackService {
    constructor($http){
        "ngInject"
        this.$http = $http
        this.baseURL = properties.CONFIG.BASE_URL+"/api";
    }

    searchTranchByName(isin){
        return this.$http({
            method: "GET",
            url:this.baseURL+"/tranche/search/name",
            params: {
                isin: isin
            }
        })
    }

    getBuyBackList(lastRecordId){
        return this.$http({
            method: "GET",
            url:this.baseURL+"/buyback/all",
            params: {
                lastRecordId: lastRecordId
            }
            })
    }
    searchByISIN(isin){
        return this.$http({
            method: "GET",
            url:this.baseURL+"/buyback/search/isin",
            params:{
                "isin": isin
            }
        })
    }
    downloadDocument(buyback, stable){
        return this.$http({
            method: "POST",
            url:this.baseURL+"/buyback/document",
            responseType: "arraybuffer",
            params:{
                "stable": stable
            },
            data: buyback
        })
    }

    getById(id){ // make strong checks on who gets calls this 
        return this.$http({
            method: "POST",
            url:this.baseURL+"/buyback/get",
            params:{
                "id": id
            }
            })
    }

    create(data){
        return this.$http({
            method: "POST",
            url: this.baseURL+"/buyback/create",
            headers:{
                "content-type": undefined
            },
            data: data
            })
    }

    update(data, mod){
        return this.$http({
            method: "POST",
            headers:{
                "content-type": undefined
            },
            url: this.baseURL+"/buyback/update",
            data: data
            })
    }

    calcTotalWithBuyback(isin){
        return this.$http({
            method: "POST",
            url: this.baseURL+"/tranche/sum",
            params: {
                isin: isin,
                ignorebuyback: false
            }
        })
    }

    downloadRedemptionPdf(buybackId){
        return this.$http({
            method: "GET",
            url: this.baseURL+"/buyback/downloadBBRedemptionPdf",
            headers: {
                'content-type': 'application/json'
            },
            params: {
                "id": buybackId
            }, 
            responseType: 'arraybuffer'
        });
    }

    downloadRBILetterPdf(buybackId){
        //console.log('RBI letter Service '+buybackId);
        return this.$http({
            method: "GET",
            url: this.baseURL + "/buyback/downloadRbiLetter",
            headers: {
                'content-type' : 'application/json'
            },
            params: {
                "id" : buybackId
            }, 
            responseType: 'arraybuffer'
        });
    }


}


