import * as properties from '../../config.properties'

export default class QuarterlyReportService {
    constructor($http) {
        "ngInject"
        this.$http = $http;
        this.baseURL = properties.CONFIG.BASE_URL + "/api/quarterlyreport/";
    }

    getQuarterlyReportData(filterQuarter, filterYear) {
        return this.$http({
            method: "GET",
            url: this.baseURL + "getQR",
            params: {
                filterQuarter: filterQuarter,
                filterYear: filterYear
            }
        })
    }

    upload(data) {
        return this.$http({
            method: "POST",
            url: this.baseURL + "upload",
            headers: {
                "content-type": undefined
            },
            data: data
        })
    }

    download(data) {
        return this.$http({
            method: "POST",
            url: this.baseURL + "download",
            headers: {
                "content-type": undefined
            },
            params: {
                attachmentId: data
            },
            responseType:'blob'
        })
    }
}


