import * as appProperties from "../../config.properties";

class AdminService {

    constructor($http) {
        "ngInject"
        this.name = "AdminService";
        this.http = $http;
    }

    getAllCustomer(customerId,pageNumber,pageSize){
        if (customerId == undefined || customerId == null) {
            customerId = "";
        }
         let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL+'/api/getAllCustomers',
           
            headers: {
                'content-type': 'application/json'
            },
            params:{
                
            }
         }
         return this.http(requestString);

     }

     getAuditDetails(customerSelected){
         let requestString = {
             method: 'GET',
             url : appProperties.CONFIG.BASE_URL+ '/admin/audit',

             headers: {
                 'content-type': 'application/json'
             },
             params: {
                 "id" : customerSelected
             }
         }

         return this.http(requestString);
     }
}

export default AdminService


