class DemoService{
    
        constructor($http){
            "ngInject"
            this.name = "demoService";
            this.http=$http;
    
        }
    
        demoCustomer(demo){
            this.http({
                method: 'POST',
                url: "http://localhost:8080/register",
                headers: {
                    'content-type': 'application/json'
                },
        
               data:demo
    
            }).then(function(response){
               
            });
        }
    
    }
    
    export default DemoService;


