import * as appProperties from "../../../config.properties";
class ReportService{

    constructor($http) {
        "ngInject"
        this.name = "reportService";
        this.http = $http;

    }


    getDeal(customerId){
      
        var payload = new FormData();
        payload.append('customerId', customerId);
        
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/getDealByCustomerId',
            headers:{
                'content-type':undefined
            },
            data:payload

        })
       
    }



    writeReport(dealNumber){
       
        $(".se-pre-con").show();

       let requestString = {
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/writeExcel',
            headers: {
                'content-type': 'application/json'
            },

            data: dealNumber,
            responseType:'blob'

        }

        $(".se-pre-con").hide();
        return this.http(requestString);
        
    }
}

export default ReportService;


