import angular from 'angular';
import uiRouter from 'angular-ui-router';
import showDealComponent from './showdeal.component';
import {default as ShowDealSevice} from './showdeal.service';

let showDealModule= angular.module('showdeal',[
    uiRouter,

])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
        .state('deal/showdeal',{
        url:'/deal/showdeal',
        component:'showDealComp'
    });
    
})
.directive('toggle', function(){
    return {
      restrict: 'A',
      link: function(scope, element, attrs){
        if (attrs.toggle=="tooltip"){
          $(element).tooltip();
        }
        if (attrs.toggle=="popover"){
          $(element).popover();
        }
      }
    };
  })

.component('showDealComp', showDealComponent)
.service('showDealService',ShowDealSevice)
.name;

export default showDealModule;


