class ManageController {
  buyBacks = []

  currentPage = 0;
	pageSize = 10;
	lastRecordId = 0;
	allLoaded = 0 ;
  constructor($state, buyBackService, crudService) {
    "ngInject"
    this.$state = $state
    this.crudService = crudService
    this.buyBackService = buyBackService
    this.mod = 'buyback';
    this.updateBuyBackTable();
  }

  updateBuyBackTable(){
    this.loadingPage = true

    try {
      this.buyBackService
      .getBuyBackList(this.lastRecordId)
        .then((response)=>{
          this.buyBacks = response.data
          if(this.buyBacks.length > 0)
           this.lastRecordId = this.buyBacks[this.buyBacks.length - 1].id;
          this.loadingPage = false
        })
    } catch (error) {
      this.loadingPage = false
    }
   
  }

  editBuyBack(buyBack){
    this.$state.go('buyback.edit', { 'edit': true, 'buyback': buyBack })
  }


  viewBuyBack(buyBack){
    this.$state.go('buyback.edit', { 'edit': true, 'buyback': buyBack, 'view':true})
  }

  /*disableBuyBack(id){
    this.crudService
    .switchStatus(id, this.mod)
      .then((response)=>{
          console.log(response)
          this.updateBuyBackTable()
      })
  }
  */

 numberOfPages(){
  if(this.buyBacks != undefined){
    return Math.ceil(this.buyBacks.length/this.pageSize);   
  }
                   
  }


  buyBackBuffer = []
  pageStateBuffer = 0
  searchByISIN(){
    if (this.search.name.length > 0){
      if(this.buyBackBuffer.length < 1){
			  this.buyBackBuffer = this.buyBacks;
			  this.pageStateBuffer = this.currentPage;
      }
      this.currentPage = 0;
      this.searching = true;
      this.buyBackService 
      .searchByISIN(this.search.name)
        .then((response)=>{
          if (this.search.name.length > 0)
							if(response.config.params.isin == this.search.name){
								this.buyBacks = response.data;
								
              }
          this.searching = false;
        })
    }else{
      this.searching = false;
		  this.currentPage = this.pageStateBuffer;
			this.buyBacks = this.buyBackBuffer;
    }
  }

  appendBuyBack(){
    console.log('here...');
		this.currentPage++;
		if(this.currentPage == this.numberOfPages() -1 ){
			this.loadingNextPage = true;
			if(this.allLoaded == 0)
      this.buyBackService
        .getBuyBackList(this.lastRecordId)
					.then((response)=>{
						if(response.data.length < 4)
							this.allLoaded = 1;
						response.data.forEach((element)=>{this.buyBacks.push(element);});
						this.loadingNextPage = false;
						this.lastRecordId = this.buyBacks[this.buyBacks.length - 1].id;
					})
			else
				this.loadingNextPage = false;
		}
  }

  downloadRedemPdf(buyback){
    //debugger;
   // console.log("Download BB Redem pdf "+ buyback);
    // status 0 is for approved
    if(buyback.status == 0){
     // console.log(buyback.id + " having status a"+ buyback.status);
      this.buyBackService.downloadRedemptionPdf(buyback.id).then((response)=>{
       // console.log("inside Response ");
        let blob = response;
        let binaryData = [];
        binaryData.push(response.binaryData);
        let a = document.createElement("a");
        a.style = "display: none";
        document.body.appendChild(a);

        let url = window.URL.createObjectURL(new Blob([response.data], { type: "application/pdf" }));
        a.href = url;
        a.download = buyback.id + '.pdf';
        a.click();
        window.URL.revokeObjectURL(url);
      });
    }else{
      // console.log(buyback.id + " having status "+ buyback.status);
      alert("Buyback is not approved so you can't download");
    }
  }

  downloadRBIPdf(buyback){
    //console.log("RBI letter: "+buyback.id);
    // status 0 is for approved
    if(buyback.status == 0){
     this.buyBackService.downloadRBILetterPdf(buyback.id).then((response)=>{
        //console.log("inside service response");
        let blob = response;
        let binaryData = [];
        binaryData.push(response.binaryData);
        let a = document.createElement("a");
        a.style = "display: none";
        document.body.appendChild(a);

        let url = window.URL.createObjectURL(new Blob([response.data], {type: "application/pdf"}));
        a.href = url;
        a.download = buyback.id + ".pdf";
        a.click();
        window.URL.revokeObjectURL(url);
     });
    }else{
      alert("Buyback is not approved so you can't download");
    }
    
  }
// -----

jumpToFirst(){
  this.pageNumber=1;
  this.getAllUsers()
}

previous(){
  this.pageNumber--;
  this.getAllUsers()
}

next(){
  this.pageNumber++;
  this.appendBuyBack();
}

}

export default ManageController;



