import angular from 'angular';
import uiRouter from 'angular-ui-router';
import addDealComponent from './addDeal.component'
import {default as AddDealService} from './addDeal.service';

var moment = require('moment');

let addDealModule = angular.module('addDeal', [
    uiRouter,
    
])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
      .state('deal/addDeal', {
        url: '/deal/addDeal',
        component: 'addDealComp'
      }).state('deal/editDeal', {
        url: '/deal/editDeal',
        component: 'addDealComp',
        params:{
          'deal': null
        }
      });
      
  })

  .component('addDealComp', addDealComponent)
  .service('dealService',AddDealService)
  .name;


  export default addDealModule;
  


