    constructor(createCPService, $stateParams, commonService, $state, $scope, $filter) {

        this.baseUrl = appProperties.CONFIG.BASE_URL;
        this.name = "createCPService";
        this.createCPService = createCPService;
        self = this;
        this.scope = $scope;
        this.state = $state;
        this.$filter = $filter;
        this.commonService = commonService;

        this.myStorage = window.localStorage;
        if (this.commonService.rolename != undefined && this.commonService.username) {

            this.myStorage.setItem("role", this.commonService.rolename);
            this.myStorage.setItem("userName", this.commonService.username);
            this.myStorage.setItem("IdList", this.commonService.IdList);
            this.myStorage.setItem("emailId", this.commonService.emailId);
        }

        this.mailId.push(this.myStorage.getItem("IdList"));
        this.role = this.myStorage.getItem("role");

        this.createCPService.getRatingAgency().then((response) => {
            this.tempArray = response.data;
            this.tempArray.forEach((element) => {
                this.ratingAgencyList.push(element);

            })
        })

        this.createCPService.getCreditRating().then((response) => {
            this.tempCreditRating = response.data;
            this.tempCreditRating.forEach((element) => {
                this.creditRatingList.push(element);
            })
        })

        if ($stateParams.cplist) {
           // debugger;
            this.isEdit = true;
            this.getOriginalRatingsByStatusAndTrancheDetails($stateParams.cplist);
            //   this.ratings = $stateParams.cplist.ratings;
            
            this.customerId = $stateParams.cplist;
            this.form = $stateParams.cplist;
            if ($stateParams.cplist.isGuaranteeLetter) {
                this.form.isGuaranteeLetter = 'true';
            }
            this.guaranteeLtrDoc = $stateParams.cplist.guaranteeLetterDoc;
            this.ratings.forEach((element) => {

                if (element.issuancePeriodIn == 'Date') {
                    this.isPeriod = true;
                }
                if (element.ratingPeriodIn == 'Date') {
                    this.isPer = true;
                }
            })
        }
    }

