    checkFileSize(doc) {

        this.showDoc = false;
        if (doc[0]) {

            var mb = 1024 * 1024;
            var sizeInMB = doc[0].size / mb;
            if (sizeInMB > 10) {
                alert("File size should not greater than 10 MB")
                document.getElementById("guaranteeLetterDoc").value = null;
            }

            var file = doc[0].name;
            var reg = /(.*?)\.(pdf|PDF|doc|docx|xls|xlsx)$/;

            if (!file.match(reg)) {
                alert("Supported only PDF,DOC,Excel File...!");
                document.getElementById("guaranteeLetterDoc").value = null;
            }
        } else {
            this.showDoc = true // To disable button when file not Choose.
        }
    }

