    numberformat() {
        var result = this.tempRating.creditRatingAmount.toString().split('.');
        var lastThree = result[0].substring(result[0].length - 3);
        var otherNumbers = result[0].substring(0, result[0].length - 3);
        if (otherNumbers != '')
            lastThree = ',' + lastThree;
        var output = otherNumbers.replace(/\B(?=(\d{2})+(?!\d))/g, ",") + lastThree;

        if (result.length > 1) {
            output += "." + result[1];
        }
        this.tempRating.creditRatingAmount = output;
    }

    validateRatingAmount() {
        //this.tempRating.creditRatingAmount = this.$filter('number')(this.tempRating.creditRatingAmount, '2');
        // var mycpamt=this.tempRating.creditRatingAmount.replace(/,/g,'');
        //this.tempRating.creditRatingAmount=mycpamt;

        if (this.tempRating.creditRatingAmount % 500000 != 0) {
            alert("Enterd Rating Amount should be in multiple of 5 lakh only...!");
            this.tempRating.creditRatingAmount = null;
            return;
        }
    }

    validateDateOfRating() {

        var currentDate = moment(moment().format('DD/MM/YYYY'), 'DD/MM/YYYY');
        var dateOfRating = moment(this.tempRating.dateOfRating, 'DD/MM/YYYY');
        var isValid = moment(dateOfRating).isAfter(currentDate);

        if (isValid) {
            alert("Date of Rating must be Before of Current date.");
            this.tempRating.dateOfRating = null;
            return;
        }

    }

    validateRatingPeriod() {
        this.isPer = false;
        if (this.tempRating.ratingPeriodIn == 'Date') {
            this.isPer = true;
            var pattern = /^(?:(?:31(\/|-|\.)(?:0?[13578]|1[02]))\1|(?:(?:29|30)(\/|-|\.)(?:0?[1,3-9]|1[0-2])\2))(?:(?:1[6-9]|[2-9]\d)?\d{2})$|^(?:29(\/|-|\.)0?2\3(?:(?:(?:1[6-9]|[2-9]\d)?(?:0[48]|[2468][048]|[13579][26])|(?:(?:16|[2468][048]|[3579][26])00))))$|^(?:0?[1-9]|1\d|2[0-8])(\/|-|\.)(?:(?:0?[1-9])|(?:1[0-2]))\4(?:(?:1[6-9]|[2-9]\d)?\d{2})$/;
            var isValid = pattern.test(this.tempRating.validityPeriod);
            if (isValid == false) {
                alert("Please Enter valid date for Validity Period of Rating");
                this.tempRating.validityPeriod = null;
                this.tempRating.ratingPeriodIn = null;
                return;
            }
        }

        else if (this.tempRating.ratingPeriodIn == 'Year') {
            this.isPer = false;
            var pattern = /^[0-9]\d{0,2}?%?$/;
            var isValid = pattern.test(this.tempRating.validityPeriod);
            if (isValid == false) {
                alert("Please Enter valid Year only up to 3 digit...for validity period of Rating");
                this.tempRating.validityPeriod = null;
                this.tempRating.ratingPeriodIn = null;
                return;
            }
        }
    }


    validatePeriodOfIssuance() {
        this.isPeriod = false;

        if (this.tempRating.issuancePeriodIn == 'Date') {
            this.isPeriod = true;
            var pattern = /^(?:(?:31(\/|-|\.)(?:0?[13578]|1[02]))\1|(?:(?:29|30)(\/|-|\.)(?:0?[1,3-9]|1[0-2])\2))(?:(?:1[6-9]|[2-9]\d)?\d{2})$|^(?:29(\/|-|\.)0?2\3(?:(?:(?:1[6-9]|[2-9]\d)?(?:0[48]|[2468][048]|[13579][26])|(?:(?:16|[2468][048]|[3579][26])00))))$|^(?:0?[1-9]|1\d|2[0-8])(\/|-|\.)(?:(?:0?[1-9])|(?:1[0-2]))\4(?:(?:1[6-9]|[2-9]\d)?\d{2})$/;
            var isValid = pattern.test(this.tempRating.validityOfIssuance);
            if (isValid == false) {
                alert("Please Enter valid date for Validity Period of Issuance");
                this.tempRating.validityOfIssuance = null;
                this.tempRating.issuancePeriodIn = null;
                return;
            }
        }

        else if (this.tempRating.issuancePeriodIn == 'Days') {

            var pattern = /^[0-9]\d{0,4}?%?$/;
            var isValid = pattern.test(this.tempRating.validityOfIssuance);
            if (isValid == false) {
                alert("Please Enter valid days only up to 5 digit...for validity period of isuance");
                this.tempRating.validityOfIssuance = null;
                this.tempRating.issuancePeriodIn = null;
                return;
            }
        }
        else if (this.tempRating.issuancePeriodIn == 'Months') {

            var pattern = /^[0-9]\d{0,2}?%?$/;
            var isValid = pattern.test(this.tempRating.validityOfIssuance);
            if (isValid == false) {
                alert("Please Enter valid months only up to 3 digit...for validity period of isuance");
                this.tempRating.validityOfIssuance = null;
                this.tempRating.issuancePeriodIn = null;
                return;
            }
        }
    }

