    getSecurityDetails(){
        console.log("customer id---"+this.customerId)
        $(".se-pre-con").show();
            this.securityDetailsService.getSecurityDetails(this.pageNumber,this.pageSize,this.customerId,this.isinNumber).then((response)=>{
                $(".se-pre-con").hide();
                this.disable=false;
                if(response.data.serviceResponse!=null && response.data.serviceStatus == "SUCCESS"){
                    this.pgNo=this.pageNumber;
                    this.pgSize=this.pageSize;
                    this.initialArray = response.data.serviceResponse.trancheList;  
                    this.arrayToDisplay=this.initialArray;
                }else if(response.data.serviceError=='No Record found'){
                    this.disable=true;
                }else{
                }  
             });  
    }

    fetchTranchRequest() {
        console.log("customer id=="+this.customerId,typeof this.customerId);
        console.log("Isin number"+this.isinNumber,typeof this.isinNumber);
        $(".se-pre-con").show();
        if( this.customerId != undefined && this.customerId != ""){
            if(this.customerId.length>=3){
                this.isinNumber=""
        this.securityDetailsService.getTranchByCustomerId(this.customerId,this.pageNumber,this.pageSize,this.isinNumber).then((response)=>{
            $(".se-pre-con").hide();
            this.disable=false;
            if(response.data.serviceResponse!=null && response.data.serviceStatus == "SUCCESS"){
                this.pgNo=this.pageNumber;
                this.pgSize=this.pageSize;
               this.searchArray = response.data.serviceResponse.trancheList;  
               this.arrayToDisplay=this.searchArray 
            }else if(response.data.serviceError=='No Record found'){
                this.disable=true;
            }else{
                
            }
         });
        }else{
            $(".se-pre-con").hide();
        }
    }else if(this.isinNumber != undefined && this.isinNumber != ""){
        if(this.isinNumber.length>=3){
            this.customerId=""
            this.securityDetailsService.getTranchByCustomerId(this.customerId,this.pageNumber,this.pageSize,this.isinNumber).then((response)=>{
                $(".se-pre-con").hide();
                this.disable=false;
                if(response.data.serviceResponse!=null && response.data.serviceStatus == "SUCCESS"){
                    this.pgNo=this.pageNumber;
                    this.pgSize=this.pageSize;
                   this.searchArray = response.data.serviceResponse.trancheList;  
                   this.arrayToDisplay=this.searchArray 
                }else if(response.data.serviceError=='No Record found'){
                    this.disable=true;
                }else{
                    
                }
             });
            }else{
                $(".se-pre-con").hide();
            }
    }
        else{
            $(".se-pre-con").hide();
            this.arrayToDisplay=this.initialArray;
        }
    }

