import * as appProperties from "../../../config.properties";
class CustomerListController{
 
    pageNumber = 1;
	pageSize = 10;
    masterArray = [];
    arrayToDisplay=[];

    customerId;
    searchArray=[];
    initialArray=[];

    constructor(listService,$state,commonService){
        this.state = $state;
        this.listService=listService;
        this.commonService = commonService;
        this.baseUrl = appProperties.CONFIG.BASE_URL;
        this.getAllCustomer();  
        this.name="customer_list";
    
    }

    getAllCustomer(){
        $(".se-pre-con").show();
        this.listService.getAllCustomer(this.customerId,this.pageNumber,this.pageSize).then((response)=>{
            $(".se-pre-con").hide();
            this.disable=false;
            if(response.data.serviceResponse!=null && response.data.serviceStatus == "SUCCESS"){
                this.pgNo=this.pageNumber;
                this.pgSize=this.pageSize;
                this.initialArray = response.data.serviceResponse.customerList;  

                this.arrayToDisplay=this.initialArray;
            }else if(response.data.serviceError=='No Record found'){
                this.disable=true;
                $(".se-pre-con").hide();
            }else{
            }
        });
    }

    fetchCustomerRequest() {
        $(".se-pre-con").show();
        if( this.customerId != undefined && this.customerId != ""){
        if(this.customerId.length>=3){
    this.listService.getCustomerByCustomerId(this.customerId,this.pageNumber,this.pageSize).then((response)=>{
        $(".se-pre-con").hide();
        this.disable=false;
        if(response.data.serviceResponse!=null && response.data.serviceStatus == "SUCCESS"){

            this.pgNo=this.pageNumber;
            this.pgSize=this.pageSize;
           this.searchArray = response.data.serviceResponse.customerList;  
           this.arrayToDisplay=this.searchArray 
        }else if(response.data.serviceError=='No Record found'){
            this.disable=true;
            
        }else{
           
        }
     });
    }
    }
    else{
       // $(".se-pre-con").hide();
        this.arrayToDisplay=this.initialArray;
        $(".se-pre-con").hide();
    }
}

    numberOfPages(){
		
        return Math.ceil(this.masterArray.length/this.pageSize);                
    }

    jumpToFirst(){
        this.pageNumber=1;
        this.getAllCustomer()
    }

    previous(){
        this.pageNumber--;
        this.getAllCustomer()
    }

    next(){
        this.pageNumber++;
        this.getAllCustomer()
    }


    editCustomer(customer,$state){
       // debugger;
        this.state.go("customers/edit", { 'customer': customer});
    }

   
    

}

CustomerListController.$inject=['listService', '$state','commonService'];
export default CustomerListController;


