import angular from 'angular';
import uiRouter from 'angular-ui-router';
import customerListComponent from './list.component';
import {default as ListService} from './list.service';

let customerListModule= angular.module('list',[
    uiRouter,
])
    .config(($stateProvider) => {
    "ngInject";
    $stateProvider
    .state('customers/list',{
        url:'/customers/list',
        component:'list'
    });
    
    
})
.directive('toggle', function(){
    return {
      restrict: 'A',
      link: function(scope, element, attrs){
        if (attrs.toggle=="tooltip"){
          $(element).tooltip();
        }
        if (attrs.toggle=="popover"){
          $(element).popover();
        }
      }
    };
  })

    .component('list', customerListComponent)
    .service('listService',ListService)
    .name;

    export default customerListModule;


