import * as appProperties from "../../../config.properties";

class feesCalculationService {
    constructor($http) {
        "ngInject"
        this.name = "feesCalculationService";
        this.$http = $http;
    }

    calculateFees(trancheId, customerId){
        return this.$http({
            method: "GET",
            url : appProperties.CONFIG.BASE_URL+ "/api/getTranchByTrancheIdAndCustomerID",
            headers:{
                "content-type": 'application/json'
            },
            params :{
               'trancheId' : trancheId,
               'customerId' : customerId
            }
        });
    }

    saveFeesDetails(data){
        //debugger;
        //console.log("Data in service "+ data);
        return this.$http({
            method: "POST",
            url : appProperties.CONFIG.BASE_URL+ "/api/feescalculation/saveFeesDetails",
            headers:{
                "content-type": 'application/json'
            },
            data : data
           
        });
    }
}

export default feesCalculationService;


