import commonService from "../../../common.service";

class ManageController {

  formData = {};

  constructor(quarterlyReportService, commonService) {

    "ngInject"

    this.commonService = commonService;
    this.qrService = quarterlyReportService;
    this.prepareReportFilter();
    this.getQuarterlyReportData();
  }

  prepareReportFilter() {
    this.fyYearMap = {};
    for (let i = 2018; i < 2025; i++) {
      // this.yearsArr.push(i);
      this.fyYearMap[i] = 'FY  ' + i + '-' + (i + 1);
    }
  }

  getQuarterlyReportData() {

    if (!this.formData.filterYear) {
      this.formData.filterYear = new Date().getFullYear();
    }

    if (!this.formData.filterQuarter) {
      const month = new Date().getMonth();
      if (month < 3) {
        this.formData.filterQuarter = "quarter04";
      }
      else if (month > 2 && month < 6) {
        this.formData.filterQuarter = "quarter01";
      }
      else if (month > 5 && month < 9) {
        this.formData.filterQuarter = "quarter02";
      }
      else {
        this.formData.filterQuarter = "quarter03";
      }
    }

    $(".se-pre-con").show();
    this.qrService
      .getQuarterlyReportData(this.formData.filterQuarter, this.formData.filterYear)
      .then((response) => {

        if (response.data.serviceStatus && response.data.serviceStatus.toLowerCase() === "success") {

          this.prepareDataForReport(response.data.serviceResponse);
          $(".se-pre-con").hide();

          const respObj = {
            "type": "warn",
            "msg": "Quarterly report data loaded successfully."
          };

          this.commonService.displayMsg(respObj);
        }
        else if (response.data.serviceStatus && response.data.serviceStatus.toLowerCase() === "fail") {
          // show error somewhere
          $(".se-pre-con").hide();
        }
      });
  }

  prepareDataForReport(respData) {
    this.customerList = respData.customerList;
    this.attachmentData = respData.attachmentData;
    this.commentData = respData.commentData;

    this.filteredCL = this.customerList;

    // Iterate over the attachment data and set the button status
    this.customerList.forEach((e, i) => {

      if (!this.attachmentData[e.customerId]) {
        this.attachmentData[e.customerId] = {};
      }

      let att = this.attachmentData[e.customerId];
      att['isButtonEnabled'] = true;

      // Disable the upload button for active and approved state
      if (att.currentState == "Active" || att.currentState == "Approve") {
        att['isButtonEnabled'] = false;
      }
    });
  }

  filterData() {

    this.filteredCL = new Array();
    if (this.searchData && this.searchData != "") {
      this.customerList.forEach((e, i) => {
        if (e.customerId && e.customerId.indexOf(this.searchData) != -1) {
          this.filteredCL.push(e);
        }
      });
    }
    else {
      this.filteredCL = this.customerList;
    }
  }

  onTableClick(e) {
    if (e.target && $(e.target).hasClass("nq-ipa-qr-attname")) {
      // Write the download logic here
      this.downloadFile(e.target.value);
    }
  }

  uploadFile(customerId) {

    let formData = new FormData()
    formData.append('selectedFile', this.selectedFile[0]);

    const data = {
      'customerId': customerId,
      'quarter': this.formData.filterQuarter,
      'year': this.formData.filterYear
    }

    formData.append('data', JSON.stringify(data));

    $(".se-pre-con").show();
    this.qrService
      .upload(formData)
      .then(response => {
        if (response.data.serviceStatus && response.data.serviceStatus.toLowerCase() === "success") {
          // Show message for approving
          this.getQuarterlyReportData();
        }
        else if (response.data.serviceStatus == "Fail") {
          // show error somewhere
          $(".se-pre-con").hide();
          const respObj = {
            "type": "warn",
            "msg": response.data.serviceError
          };

          this.commonService.displayErrorMsg(respObj);
        }

      })
  }

  downloadFile(attId) {

    let att;
    
    this.customerList.forEach((e, i) => {
      let attachment = this.attachmentData[e.customerId];
      if(attId == attachment.id){
        att = attachment;
      }
    });

    if(att){
      $(".se-pre-con").show();
      this.qrService
      .download(attId)
      .then(response => {
        if (response.data.serviceStatus == "Fail") {
          // show error somewhere
          $(".se-pre-con").hide();
        }
        else {
          $(".se-pre-con").hide();
          this.commonService.buildAndPushFile(response.data, att.attachmentName);
        }
      });
    }
    else {
      const respObj = {
        "type": "warn",
        "msg": "Error while downloading the quarterly report. Please try again or contact administrator."
      };

      this.commonService.displayMsg(respObj);
    }
  }
}

export default ManageController;



