import * as appProperties from "../../../config.properties";

class ShowBrDetailsController{

    pageNumber = 1;
	pageSize = 10;
    masterArray = [];
    arrayToDisplay=[];

    customerId;
    initialArray=[];
    searchArray=[]


    constructor(showBrService,$state,commonService){
        this.name='showBrDetails';
        this.state = $state;
        this.showBrService=showBrService;
        this.commonService = commonService;
        this.baseUrl = appProperties.CONFIG.BASE_URL;
        // this.myStorage = window.localStorage;
        // if(this.commonService.rolename!=undefined){
        //     this.myStorage.setItem("role", this.commonService.rolename);
        // }
        // this.role=this.myStorage.getItem("role")
       

        this.getBRDetails();
        
      
    }

    getBRDetails(){

        $(".se-pre-con").show();
        this.showBrService.getBRDetails(this.customerId,this.pageNumber,this.pageSize).then((response)=>{
           
            
            this.disable=false;
            $(".se-pre-con").hide();
            if(response.data.serviceResponse!=null && response.data.serviceStatus == "SUCCESS"){

                this.pgNo=this.pageNumber;
                this.pgSize=this.pageSize;
                this.initialArray = response.data.serviceResponse.BRList;  
                this.arrayToDisplay=this.initialArray;
                
                
            }else if(response.data.serviceError=='No Record found'){
               
                this.disable=true;
               

            }else{
                alert("Internal server error");
                
            }

        })
        .catch(err => {
            console.log('mainerror', err);
            $(".se-pre-con").hide();
        });

    }


    fetchBrRequest() {
        $(".se-pre-con").show();
        if( this.customerId != undefined && this.customerId != ""){
        if(this.customerId.length>=3){
    this.showBrService.getBrByCustomerId(this.customerId,this.pageNumber,this.pageSize).then((response)=>{
        $(".se-pre-con").hide();
        this.disable=false;
        if(response.data.serviceResponse!=null && response.data.serviceStatus == "SUCCESS"){

            this.pgNo=this.pageNumber;
            this.pgSize=this.pageSize;
           this.searchArray = response.data.serviceResponse.brList;  
           this.arrayToDisplay=this.searchArray 
        }else if(response.data.serviceError=='No Record found'){
            this.disable=true;
            
        }else{
           
        }
     });
    }
    }
    else{
       // $(".se-pre-con").hide();
        this.arrayToDisplay=this.initialArray;
        $(".se-pre-con").hide();
    }
}

    numberOfPages(){
		
        return Math.ceil(this.masterArray.length/this.pageSize);                
    }

    jumpToFirst(){
        this.pageNumber=1;
        this.getBRDetails()
    }

    previous(){
        this.pageNumber--;
        this.getBRDetails()
    }

    next(){
        this.pageNumber++;
        this.getBRDetails()
    }

    EditBR(br,$state){

        // if(br.status=='CallBack' || br.status=='Approved'){
        //     this.state.go("boardResolution/edit", { 'br': br } )
        // }else{
        //     alert("You can't update data ...data is not approved or callbacked");
        // }

        this.state.go("boardResolution/edit", { 'br': br } )
    }

   

}

ShowBrDetailsController.$inject =['showBrService','$state','commonService'];
export default ShowBrDetailsController;


