class ManageController {
  investors = []

  currentPage = 0;
	pageSize = 10;
	lastRecordId = 0;
	allLoaded = 0 ;
  constructor($state, investorService) {
    "ngInject"
    this.$state = $state
    this.investorService = investorService
    this.mod = 'investor';
    this.updateInvestorTable();
  }

  updateInvestorTable(){
    this.loadingPage = true
    try {
      this.investorService
      .getInvestorList(this.lastRecordId)
        .then((response)=>{
          if(response.status == 500)  {
            this.loadERROR = true
          }else{
            this.investors = response.data
            if(this.investors.length > 0)
              this.lastRecordId = this.investors[this.investors.length - 1].id;
            this.loadERROR = false
            this.loadingPage = false
          }
          
        })
    } catch (error) {
      this.loadingPage = false
    }
    
  }

  editInvestor(investor){
    this.$state.go('investor.edit', { 'edit': true, 'investor': investor })
  }


  viewInvestor(investor){
    this.$state.go('investor.edit', { 'edit': true, 'investor': investor, 'view':true})
  }

  /*disableInvestor(id){
    this.crudService
    .switchStatus(id, this.mod)
      .then((response)=>{
          console.log(response)
          this.updateInvestorTable()
      })
  }
  */

 numberOfPages(){
  if(this.investors != undefined){
    return Math.ceil(this.investors.length/this.pageSize);   
  }
                   
  }


  investorBuffer = []
  pageStateBuffer = 0
  searchByName(){
    if (this.search.name.length > 0){
      if(this.investorBuffer.length < 1){
			  this.investorBuffer = this.investors;
			  this.pageStateBuffer = this.currentPage;
      }
      this.currentPage = 0;
      this.searching = true;
      this.investorService 
      .searchByName(this.search.name)
        .then((response)=>{
          if (this.search.name.length > 0)
							if(response.config.params.name == this.search.name){
								this.investors = response.data;
								
              }
          this.searching = false;
        })
    }else{
      this.searching = false;
		  this.currentPage = this.pageStateBuffer;
			this.investors = this.investorBuffer;
    }
  }

  appendInvestors(){
		this.currentPage++;
		if(this.currentPage == this.numberOfPages() -1 ){
			this.loadingNextPage = true;
			if(this.allLoaded == 0)
      this.investorService
        .getInvestorList(this.lastRecordId)
					.then((response)=>{
						if(response.data.length < 4)
							this.allLoaded = 1;
						response.data.forEach((element)=>{this.investors.push(element);});
						this.loadingNextPage = false;
						this.lastRecordId = this.investors[this.investors.length - 1].id;
					})
			else
				this.loadingNextPage = false;
		}
  }
  
  
}

export default ManageController;



