    constructor(createService, $stateParams, $state, commonService,$filter) {
        this.createService = createService;
        this.state = $state;
        this.name = "customers_create";
        
        this.commonService = commonService;
        this.baseUrl = appProperties.CONFIG.BASE_URL;
        this.$filter = $filter;
        

        this.myStorage = window.localStorage;
        if (this.commonService.rolename != undefined && this.commonService.username) {
            this.myStorage.setItem("role", this.commonService.rolename);
            this.myStorage.setItem("userName", this.commonService.username);
            this.myStorage.setItem("IdList", this.commonService.IdList);
            this.myStorage.setItem("emailId", this.commonService.emailId);
        }

        this.mailId.push(this.myStorage.getItem("IdList"));
        this.role = this.myStorage.getItem("role");
        if ($stateParams.customer) {
            this.isEdit = true;
            this.isView = true;
            this.form = $stateParams.customer;
            console.log(this.form);
            this.ipaDoc = $stateParams.customer.ipaAggrementDoc;
            this.emailFaxDoc = $stateParams.customer.emailFaxEndemnityDoc;
            this.form.isUserActive = $stateParams.customer.isUserActive;
            //this.form.isNotAxisAccount = false;

            if ($stateParams.customer.isIPAAggrement) {
                this.form.isIPAAggrement = 'true';
            }
            if ($stateParams.customer.isDefaulter) {
                this.form.isDefaulter = 'true';
            }
            if($stateParams.customer.ipaAgreementExpiryLimit=='Unlimited'){
                this.isExpiry=true;
            }
        }
    }


    
    emailValidation($event) {
        var input = this.form.email;
        var regex = /^[\w\-\.\+]+\@[a-zA-Z0-9\.\-]+\.[a-zA-z0-9]{2,4}$/;
        if (regex.test(input)) {
            this.validMail = true;
        } else {
            this.validMail = false;
        }
    }

    onloadForm(){
       // console.log("after loading form " + this.form);
        this.notAxisBankStatusChange();
    }

    notAxisBankStatusChange(){
        $(".nq-ipa-cust-edit-btn").hide();
        $(".nq-ipa-cust-edit-na-btn").hide();

        if(this.role === "ipa_maker" && this.isEdit){
            if (this.form.isNotAxisAccount){
                $(".nq-ipa-cust-edit-na-btn").show();
            }
            else{
                $(".nq-ipa-cust-edit-btn").show();
            }
        }
    }

    change(field) {
        //debugger;
        // this.form.isTDSActive = false;
        this.isTDSEmpty = false;
        if(this.form.isTDS){
            this.isTDSEmpty = true;
            // console.log("IF chn() : this.isTDSEmpty :"+ this.isTDSEmpty);
            // console.log("IPA Fees : "+ this.form.ipaFees);
            // console.log("GST : "+ this.form.gst );
            // console.log("Total Fees : "+ this.form.totalFees);
            // console.log("% TDS : "+ this.form.tdsAmt);
            // console.log("TDS Value : "+ this.form.tds);
        }else{
            this.isTDSEmpty = false;
            //console.log("Else chn() : this.isTDSEmpty :"+ this.isTDSEmpty);
            this.form.tds = null;
            this.form.tdsAmt = null;
            this.form.totalFees = parseInt(this.form.ipaFees) + this.form.gst
        }

        if(!this.isTDSEmpty){
            // console.log("Inside close toggle ops");
            // console.log("% TDS : "+ this.form.tdsAmt);
            // console.log("TDS Value : "+ this.form.tds);
           
        }
        
      

        if (field = !this.form.isEmailFaxEndemnity && this.role == 'ipa_maker') {
            document.getElementById("emailFaxEndemnityDoc").value = null;
        }
        if (field = !this.form.isDefaulter && this.role == 'ipa_maker') {
            this.form.defaultDate = null;
            this.form.defaultPeriodEndDate = null;
        }
    }

    checkFileSize(doc) {
        var mb = 1024 * 1024;
        var sizeInMB = doc[0].size / mb;
        if (sizeInMB > 10) {
            alert("File size should not greater than 10 MB")
            document.getElementById("ipaDoc").value = null;
        }
    }

    calTds(){
            // this.form.gst = this.form.ipaFees * 18 / 100;
            // this.form.tds = this.form.ipaFees + this.form.gst - this.form.tdsAmt;
            
            // this.form.totalFees = this.form.ipaFees + this.form.gst;
            
               // this.form.tds = null;
                //this.form.totalFees = this.form.ipaFees + this.form.gst
                this.form.totalFees = parseInt(this.form.ipaFees) + this.form.gst
            
                this.form.tds = this.form.ipaFees * this.form.tdsAmt / 100;
                //this.form.totalFees = this.form.ipaFees + this.form.gst - this.form.tds;
                
                this.form.totalFees = (parseInt(this.form.ipaFees) + this.form.gst) - this.form.tds;
                if(this.form.isTDS && this.form.tdsAmt){
                    this.isTDSEmpty = false;
                    console.log("If : isTDS "+ this.form.isTDS + " && tdsAmt "+ this.form.tdsAmt + " isTDSEmp : "+ this.isTDSEmpty);
                }else{
                    this.isTDSEmpty = true;
                    console.log("Else : isTDS "+ this.form.isTDS + " && tdsAmt "+ this.form.tdsAmt + " isTDSEmp : "+ this.isTDSEmpty) ;
                }
            
    }


    calculateFees() {
       // debugger;
        var gst = 0;
        var totalIPAFees = 0;
        gst = this.form.ipaFees * 18 / 100;
        if (this.form.ipaFees != null) {
            this.form.gst = gst;
            totalIPAFees = this.form.ipaFees + gst;
            this.form.totalFees = totalIPAFees;
        }
        if (this.form.ipaFees == null) {
            this.form.gst = null;
            this.form.totalFees = null;
        }
        if (this.form.isTDS == true) {
            var tds = this.form.ipaFees + this.form.gst - this.form.tds;
            this.form.tds = tds;
        }
    }

    validateDefaulterDate() {   
        var date1 = moment(this.form.defaultDate, 'DD/MM/YYYY');
        var date = new Date(date1);
        var diffDate = date.setMonth(date.getMonth() + 6);
        var tempDate = moment(diffDate).format('DD/MM/YYYY');
        this.form.defaultPeriodEndDate = tempDate;
    }
    
    checkedIPA(){
        if(this.expiry==true){
            this.isChecked=true;
            this.form.ipaAgreementExpiryLimit="Unlimited";
        }else{
            this.isChecked=false;
            this.form.ipaAgreementExpiryLimit=null;
        }
    }
    
    checkedExpiryLimit(){
        if(this.form.ipaAggrementExpirydate==''){
            this.isCheckedExpiry=false;
        }else{
            this.isCheckedExpiry=true;
        }
    }
  
    isIpaFeqDisable = false;
    validateIpaFrequency() {
        if (this.form.ipaFrequency == 'percentFees') {
            this.isIpaFeqDisable = true;
            this.form.ipaFees = 0.0;
            this.form.gst = 0.0;
            this.form.totalFees = 0;
        } else {
            this.isIpaFeqDisable = false;
            this.form.ipaFees = null;
            this.form.gst = null;
            this.form.totalFees = null;
        }
    }

    back() {
        this.state.go('requests/request');
    }

    cancelled() {
        this.state.go('customers/list');
    }

