import angular from 'angular';
import uiRouter from 'angular-ui-router';
import Common from './common/common';
import Components from './components/components';
import AppComponent from './app.component';
import AppService from './app.service';
import CommonService from './common.service';
import  ServiceInterceptor from './app.interceptor';
import CommonStorage from './common/common-services/storage.js'
import crudService from './crud.service'
//import ManageCrDrService from './components/CreditDebit/manage/managecrdr.service'
require('angucomplete-alt');
import 'normalize.css';

angular.module('app', [
    uiRouter,
    Common,
    Components,
    "angucomplete-alt",
    
  ])
  .config(($locationProvider, $httpProvider) => {
    "ngInject";
    // @see: https://github.com/angular-ui/ui-router/wiki/Frequently-Asked-Questions
    // #how-to-configure-your-server-to-work-with-html5mode
    $locationProvider.html5Mode(true).hashPrefix('!');
    $httpProvider.interceptors.push('app_intercept');
  })

  .component('app', AppComponent)
  .service('app_intercept', ServiceInterceptor)
  .service("appService",['$http', '$q', AppService])
  .service("commonService",['$http', '$q', CommonService])
  .service("commonStorage",CommonStorage)
  .service("crudService",crudService)
  .filter('INR', function () {        
    return function (input) {
        if(input  != null )        // to ignore the replace error
          input = parseInt(input.toString().replace(/,/g, '')) 
        if (!isNaN(input) && input !== null) {
            var result = input.toString().split('.');
            if(result[0].includes(',')){
                result[0]=parseInt(input.replace(/,/g,''));
            }
            var lastThree = result[0].substring(result[0].length - 3);
            var otherNumbers = result[0].substring(0, result[0].length - 3);
            if (otherNumbers != '')
                lastThree = ',' + lastThree;
            var output = otherNumbers.replace(/\B(?=(\d{2})+(?!\d))/g, ",") + lastThree;
            if (result.length > 1) {
                output += "." + result[1];
            }            
            return  output;
        }
    }
})

.filter('startFrom', ()=>{ // for paginination ... remove if unnesssary
    return (input, start) =>{
    if(input != undefined ||  input != null)
      return input.slice(start);
  }
  });



