import * as appProperties from "../../../config.properties";

class CreateCPService {

    constructor($http) {
        "ngInject"
        this.name = "createCPService";
        this.http = $http;
    }

    getOriginalRatingsByStatusAndTrancheDetails(cp){
        $(".se-pre-con").show();
       var cpDetails = {
            "cpId" : cp.id
       }

       return this.http({
        method: 'POST',
        url: appProperties.CONFIG.BASE_URL + '/api/getOriginalRatingDetailsByCp',
        headers: {
            'content-type': 'application/json'
            //'content-type': undefined
        },
       
        params: cpDetails
       
    });
    }

    getCustbyID(details) {

        return this.http({
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL + '/api/getById?customerId=' + details,
            headers: {
                'content-type': 'application/json'
            },

            data: details
        });
    }

    createCP(form) {
        $(".se-pre-con").show();
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/registerCP',
            headers: {
                'content-type': undefined
            },

            data: form


        })

    }


    updateCPDetails(form) {

        $(".se-pre-con").show();
        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/updateCP',
            headers: {
                'content-type': undefined
            },

            data: form


        })
    }

    getRatingAgency(){

        let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL+ '/api/getAgencyName',
            headers: {
                'content-type': 'application/json'
            },
           
        }
        return this.http(requestString);

    }

    getCreditRating(){
        
        let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL+ '/api/getCreditRating',
            headers: {
                'content-type': 'application/json'
            },
           
        }
        return this.http(requestString);
    }


    uploadDocument(guaranteeLetterDoc, fileList, trancheId, dealNumber, customerId) {

        var payload = new FormData();
        if(guaranteeLetterDoc!=null){
            payload.append('guaranteeLetterDoc', guaranteeLetterDoc[0]);
        }
        
        payload.append('trancheId', trancheId);
        payload.append('dealNumber', dealNumber);
        payload.append('customerId', customerId);


        var files = [];
        for (var i = 0; i < fileList.length; i++) {
            if(fileList[i]!=undefined){
                payload.append('fileList', fileList[i][0]);
            }
           
        }

        return this.http({
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/uploadDocument',
            processData: false,
            headers: {
                'content-type': undefined
            },
            data: payload
        })
    }

    downloadDocument(fileName,customerId,cpNumber){
    
        return this.http({
            method: 'Post',
            url: appProperties.CONFIG.BASE_URL+'/api/downloadCPDoc',
            processData: false,
            headers:{
                'content-type': 'application/json'
            },
            params:{
                "fileName":fileName,
                "customerId":customerId,
                "cpNumber":cpNumber,
                
            },
            responseType:'blob'
           
        })
       
    }

}


export default CreateCPService;


