import * as appProperties from "./config.properties";
var self;
class CommonService {
    loggerid;
    token;
    lastTokenGenerated;
    rolename;
    IdList = [];
    emailId;
    constructor($http) {
        this.name = "commonService";
        this.$http = $http;
        var decodedCookie = decodeURIComponent(document.cookie);
        var ca = decodedCookie.split(';');
        this.myStorage = window.localStorage;
        var temp1 = "loggedId=";
        var temp2 = "access-token=";
        ca.forEach((element) => {
            var tempid = element.substr(1, 9);
            var temptoken = element.substr(1, 13);
            if (temp1 == tempid) {
                var loggid111 = element.replace("loggedId=", "").trim();
                this.loggerid = loggid111;

            }
            if (temp2 == temptoken) {
                var token111 = element.replace("access-token=", "").trim();
                this.token = token111;
            }

        }
        )
    }

    setRoleName(rolename) {
        this.rolename = rolename;
    }

    setEmailId(email) {
        this.emailId = email;
    }

    getRoleName() {
        return this.rolename;
    }


    getUserById() {

        return this.$http({
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL + '/api/getUserDetails',
            headers: {
                'content-type': 'application/json',
                // 'Authorization': 'Token '+this.token,

            }
        }).then((response) => {
            this.userdetails = response.data.serviceResponse;
            this.username = this.userdetails.username;
            var role = this.userdetails.roles;
            var roleid;
            var rolename;
            this.lastTokenGenerated = this.userdetails.lastTokenGenerated;
            role.forEach(element => {   // here we get the array of roles so we iterarte it
                roleid = element.id;
                rolename = element.name;
                if (rolename == "ipa_checker") {
                    this.setRoleName(element.name);
                }
                else if (rolename == "ipa_maker") {
                    this.setRoleName(element.name);
                    this.setEmailId(this.userdetails.email);
                }

            });

            return this.getRoleName();
        });


    }

    getAllCheckersDetails(userRole) {

        return this.$http({
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL + '/api/getAllCheckersId',
            headers: {
                'content-type': 'application/json',
                'Authorization': 'Token ' + this.token,
            },
            params: {
                'userRole': userRole,
            }
        }).then((response) => {

            this.userData = response.data.serviceResponse;
            this.userData.forEach((element) => {
                element.forEach((result) => {
                    this.keys = Object.keys(result)
                    this.keys.forEach((temp) => {
                        if (temp == 'email') {
                            this.IdList.push(result.email);
                        }
                    })
                })
            })
            this.myStorage.setItem("IdList", this.IdList);

        })
    }

    buildAndPushFile(result, name) {
        // be careful of scopes, might cause file leakages
        let file = new Blob([result], {});
        let fileURL = (window.URL || window.webkitURL).createObjectURL(file);
        let link = angular.element('<a/>');
        link.attr({
            href: fileURL,
            download: name
        })[0].click();
    }

    displayMsg(input) {

        $(".nq-ipa-errbox-msgbox").text(input.msg);
        $(".nq-ipa-errbox").show();

        let errorTime = 5000;
        if (input.type == "warn") {
            $("nq-ipa-errbox-img").addClass("warn");
            errorTime = 10000;
        }

        setTimeout(() => {
            $(".nq-ipa-errbox").hide();
        }, errorTime);
    }

}
export default CommonService;


