    inputChanged(item) {

        if (item) {
            self.data = item.originalObject;
        }
        self.isUserActive = self.data.commercialPaper.customer.isUserActive;

        //debugger;
        if (self.data.deal != null && self.data.deal.status != 'Rejected') {
            alert("Deal is already Registerd for Treanch ID:" + self.data.trancheId + " ...!");
            self.scope.$broadcast('angucomplete-alt:changeInput', 'TrancheSecurity', ' ');
            self.data = null;
            return;
        }

        if (self.data.status == 'Pending' || self.data.status == 'CallBack') {
            alert("Tranche is not Approved For Deal...!");
            self.scope.$broadcast('angucomplete-alt:changeInput', 'TrancheSecurity', ' ');
            self.data = null;
            return;
        }

        /** Find Tenor in Days */
        var maturityDate = moment(self.data.maturityDate, 'DD/MM/YYYY');
        var valueDate = moment(self.data.valueDate, 'DD/MM/YYYY');
        self.tenorInDays = maturityDate.diff(valueDate, 'days');

        // % base Calc on Tranch Amt

        var custData = self.data.commercialPaper.customer;
        if (custData.ipaFrequency == 'percentFees') {
            var calFees = (self.data.totalCPIssue * 0.010 * parseInt(self.tenorInDays) / 365 * 100);
            var feeWithGST = (calFees * 18) / 100;
            self.ipaFeeAmount = calFees + feeWithGST;
            if (custData.ipaFrequency == 'percentFees' && custData.isTDS == true) {
                var feeWithTDS = (calFees * 10) / 100
                self.ipaFeeAmount = calFees - feeWithTDS;
            }
        }


        /** Populate BR Details */
        var brData = self.data.commercialPaper.customer.br
        brData.forEach((element) => {
            self.tempBrData = element;

        })

        /** Populate Rating Details */
        self.tempRating = self.data.commercialPaper.ratings;

    }

