    validateCPIssue() {

        if (this.transaction.cpIssue.includes(',')) {
            this.transaction.cpIssue = this.transaction.cpIssue.replace(/,/g, '');
        }
        if (typeof this.data.totalCPIssue != 'number')
            if (this.data.totalCPIssue.includes(',')) {
                this.data.totalCPIssue = this.data.totalCPIssue.replace(/,/g, '');
            }

        if (parseInt(this.transaction.cpIssue) > parseInt(this.data.totalCPIssue)) {
            alert("cpIssue Value can't exceed than Total CP issue value");
            this.transaction.cpIssue = this.transaction.cpIssue.slice(0, -1);
            return;
        }


        /** Calculate Unit*/
        var calUnit = 500000;

        this.transaction.units = parseInt(this.transaction.cpIssue) / calUnit;

        /** Calculate Stamp Duty*/
        var maturityDate = moment(self.data.maturityDate, 'DD/MM/YYYY');
        var valueDate = moment(self.data.valueDate, 'DD/MM/YYYY');

        // var diffMonths = moment(maturityDate).diff(moment(valueDate), 'months', true);

        var diffMonths = Math.ceil(moment(maturityDate).diff(moment(valueDate), 'months', true));

        /* Open in FY 2020-2021 */
        // this.transaction.stampDuty = ((this.transaction.discountedValue) * 0.005)/100;

        if (diffMonths <= 3) {
            var stampDuty = (this.transaction.cpIssue * 0.12) / 1000;
            this.transaction.stampDuty = stampDuty;
        }
        else if (diffMonths > 3 && diffMonths <= 6) {
            var stampDuty = (this.transaction.cpIssue * 0.24) / 1000;
            this.transaction.stampDuty = stampDuty;
        }
        else if (diffMonths > 6 && diffMonths <= 9) {
            var stampDuty = (this.transaction.cpIssue * 0.36) / 1000;
            this.transaction.stampDuty = stampDuty;
        }
        else if (diffMonths > 9 && diffMonths <= 12) {
            var stampDuty = (this.transaction.cpIssue * 0.50) / 1000;
            this.transaction.stampDuty = stampDuty;
        }
    }

    calculationOnPrice() {

        var discValue = 0;
        if (this.transaction.cpIssue.includes(',')) {
            this.transaction.cpIssue = this.transaction.cpIssue.replace(/,/g, '');
        }
        discValue = (this.transaction.cpIssue * this.transaction.discountedPrice) / 100;
        this.transaction.discountedValue = discValue;

        var discRate = 0;
        discRate = (((100 - this.transaction.discountedPrice) / 100) * (365 / this.tenorInDays)) * 100;
        this.transaction.discountedRate = parseFloat(discRate.toFixed(2));
        //this.investor.discountedRate = parseFloat(discRate);

        var yieldvalue = 0;
        yieldvalue = (this.transaction.cpIssue * this.transaction.discountedRate) / this.transaction.discountedValue;
        // this.investor.yeild=yieldvalue
        this.transaction.yeild = parseFloat(yieldvalue.toFixed(2));

        if (this.transaction.discountedPrice == null || this.transaction.discountedPrice == 0) {
            this.transaction.discountedValue = null;
            this.transaction.discountedRate = null;
        }

    }

    calculationOnRate() {
        debugger;
        var discPrice = 0;
        if (typeof this.transaction.cpIssue != 'number') {
            if (this.transaction.cpIssue.includes(',')) {
                this.transaction.cpIssue = this.transaction.cpIssue.replace(/,/g, '');
            }
        }

        discPrice = (100 - ((this.transaction.discountedRate * 100 * this.tenorInDays) / (365 * 100))).toFixed(4);
        this.transaction.discountedPrice = parseFloat(discPrice);

        var discValue = 0;
        discValue = (this.transaction.cpIssue * this.transaction.discountedPrice) / 100;
        this.transaction.discountedValue = discValue;


        var yieldvalue = 0;
        yieldvalue = (this.transaction.cpIssue * this.transaction.discountedRate) / this.transaction.discountedValue;
        //this.investor.yeild=yieldvalue
        this.transaction.yeild = parseFloat(yieldvalue.toFixed(2));


        if (this.transaction.discountedRate == null || this.transaction.discountedRate == 0) {
            this.transaction.discountedPrice = null;
            this.transaction.discountedValue = null;
        }

    }
    ValidatePromissoryDate() {
        var isSame = moment(this.data.valueDate, 'DD/MM/YYYY').isSame(moment(this.form.promissoryDate, 'DD/MM/YYYY'))
        if (isSame == false) {
            alert("Promissory Date should be same as Value date...!");
            this.form.promissoryDate = null;
            return;
        }
    }

    validateDealConfDate() {
        var isBefore = moment(moment(this.transaction.dealConfirmationDate, 'DD/MM/YYYY')).isSameOrBefore(moment(this.data.valueDate, 'DD/MM/YYYY'));
        if (isBefore == false) {
            alert("Deal Confirmation Date should be Same or less than Value date...!");
            this.transaction.dealConfirmationDate = null;
            return;
        }

    }

    validateContractDate() {
        var isBefore = moment(moment(this.transaction.dateOfContract, 'DD/MM/YYYY')).isSameOrBefore(moment(this.data.valueDate, 'DD/MM/YYYY'));
        if (isBefore == false) {
            alert("Date of Contract should be same or less than Value date...!");
            this.transaction.dateOfContract = null;
            return;
        }
    }

