class CreateController {

  /*investor = {
  "name":"Jonathan Doe hello world !",
  "dpId":"DP89237821",
  "dpName":"Jonathan Doe",
  "clientId":"CLI283912",
  "contactNumber":"8883838383",
  "email":"j.do@neoquant.com",
  "customerName":"Jonathan Doe"
  ,"bankIFSC":"IFSC23131",
  "bankAccountNumber":"839493289823",
  "bankName":"State Bank Of India",
  "createdDate":null,
  "lastModified":"17-10-2018 12:41:10"}*/
  constructor($stateParams, crudService) {
    "ngInject"
    this.crudService = crudService
    this.mod="investor"

    this.$stateParams=$stateParams;
    if($stateParams.edit){
      this.isEditing=$stateParams.edit;
      this.investor=$stateParams.investor;
      this.viewing=$stateParams.view;
      this.chk=$stateParams.chk;
    }
  }


  submit(form){
    
    this.dupDetails = false
    this.requesting  = true;
    try{
      if(this.isEditing){
        this
        .crudService
          .update(this.investor, this.mod)
            .then((response)=>{
              if(response.status == 200)
                this.submitResponse = true;
              else
                this.submitResponse = false;
  
              this.requesting = false;
            });
      }else{
        this
        .crudService
          .create(this.investor, this.mod)
            .then((response)=>{
              if(response.status == 200){
                this.submitResponse = true;
                this.investor = {}
                for(let key in form){
                  if(!key.startsWith("$")) {// keys defined by user
                    form[key].$pristine = true;
                  }
                }
              }else if(response.status == 400){
                console.log('caught')
                this.dupDetails = true
              }else
                this.submitResponse = false;
              this.requesting = false; 
            });
      }
    }catch(err){
      this.requesting = false
    }
    
    
  }


}

export default CreateController;



