var moment = require('moment');
class ManageController {
  benpos = []

  currentPage = 0;
	pageSize = 10;
	lastRecordId = 0;
	allLoaded = 0 ;
  constructor(benposService) {
    "ngInject"
    
    this.benposService = benposService
    this.updateBenposTable()
  }

  updateBenposTable(){
    this.loadingPage = true

    try {
      this.benposService
      .getBenposList(this.lastRecordId)
        .then((response)=>{
          if(response.status == 500)  {
            this.loadERROR = true
          }else{
            this.benpos = response.data
            if(this.benpos.length > 0)
            this.lastRecordId = this.benpos[this.benpos.length - 1].id;
            this.loadERROR = false
            this.loadingPage = false
          }
          
        })
    } catch (error) {
      this.loadingPage = false
    }
    
  }
  numberOfPages(){
    if(this.benpos != undefined){
      return Math.ceil(this.benpos.length/this.pageSize);   
    }
                     
    }

  uploadBenpos(){
    this.uploadingFile = true
    let formData =new FormData()
    formData.append('benpos', this.document[0]);

    try{
      this.benposService
      .upload(formData)
        .then(response=>{
          console.log(response)
          if(response.status == 200){
            this.downloadSuccess = true
          }else if(response.status == 400){
            this.downloadSuccess = false
            this.downloadError = true
            this.uploadStatus = response.data
          }else if(response.status == 500){
            this.downloadSuccess = false
            this.downloadError = true
            this.uploadStatus = {"err": "internal server error"}
          }
          this.uploadingFile = false
        })
    }catch(err){
      console.log("error caught !")
    }
    
  }


  downloadEPH(benpos){
    benpos.downloading = true;
    this.benposService
      .downloadEPH(benpos.id)
        .then((response)=>{
          console.log(moment)
          this.buildAndPushFile(response.data, benpos.isin+"__"+moment(new Date()).format("DDMMYYYY")+".xlsx")
          benpos.downloading = false
        })
  }

  buildAndPushFile(result, name){
    // be careful of scopes, might cause file leakages
      let file = new Blob([result], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8" });
      let fileURL = (window.URL || window.webkitURL).createObjectURL(file);
      let link = angular.element('<a/>');
      link.attr({
        href: fileURL,
        download: name
      })[0].click();
  }

  appendBenpos(){
		this.currentPage++;
		if(this.currentPage == this.numberOfPages() -1 ){
			this.loadingNextPage = true;
			if(this.allLoaded == 0)
      this.benposService
        .getBenposList(this.lastRecordId)
					.then((response)=>{
						if(response.data.length < 4)
							this.allLoaded = 1;
						response.data.forEach((element)=>{this.benpos.push(element);});
						this.loadingNextPage = false;
						this.lastRecordId = this.benpos[this.benpos.length - 1].id;
					})
			else
				this.loadingNextPage = false;
		}
  }

  benposBuffer = []
  pageStateBuffer = 0
  searchByISIN(){
    if (this.search.name.length > 0){
      if(this.benposBuffer.length < 1){
			  this.benposBuffer = this.benpos;
			  this.pageStateBuffer = this.currentPage;
      }
      this.currentPage = 0;
      this.searching = true;
      this.benposService 
      .searchByISIN(this.search.name)
        .then((response)=>{
          if (this.search.name.length > 0)
							if(response.config.params.isin == this.search.name){
								this.benpos = response.data;
								
              }
          this.searching = false;
        })
    }else{
      this.searching = false;
		  this.currentPage = this.pageStateBuffer;
			this.benpos = this.benposBuffer;
    }
  }
  
}

export default ManageController;



