import * as appProperties from "../../../config.properties";

class ShowDealSevice{

    constructor($http){
        "ngInject"
        this.name="showDealService";
        this.http=$http;
    }



    getDealByCustomerId(customerId, pageNumber, pageSize, isinNumber) {
        console.log(typeof isinNumber)
        
        console.log("page sise is - at service--" + pageSize);
        console.log(appProperties.CONFIG.BASE_URL + '/api/getRequestDetails');
        let requestString = {
            method: 'POST',
            url: appProperties.CONFIG.BASE_URL + '/api/getRequestDetails',
            headers: {
                'content-type': 'application/json'
            },
            params: {
                "customerId": customerId,
                "isinNumber": isinNumber,
                "modelName": "Deal",
                pageNumber: pageNumber,
                pageSize: pageSize
            }
        }
        return this.http(requestString);
    }

    getAllDeals(pageNumber,pageSize,customerId, isinNumber){
        if (isinNumber == undefined || isinNumber == null) {
            isinNumber = ""
            if (customerId == undefined || customerId == null) {
                customerId = "";
            }
        }
        if (customerId == undefined || customerId == null) {
            customerId = "";
            if (isinNumber == undefined || isinNumber == null) {
                isinNumber = ""
            }
        }
       
         let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL+'/api/viewAllDeal',
            headers: {
                'content-type': 'application/json'
            },
            params:{
                pageNumber: pageNumber,
                pageSize: pageSize,
                "customerId": customerId,
                "isinNumber": isinNumber
            }
        }

        return this.http(requestString);

     }


}

export default ShowDealSevice


