import * as properties from './config.properties'

export default class CrudService{
    
    constructor($http){
        "ngInject"
        this.$http = $http
        //forward slath '/' not appended in base url, LOOK HERE incase of URL erros 
        this.baseURL = properties.CONFIG.BASE_URL+"/api/" ; 
    }
    
    create(data, mod){
        return this.$http({
            method: "POST",
            url: this.baseURL+mod+"/create",
            data: data
            })
    }

    update(data, mod){
        return this.$http({
            method: "POST",
            url: this.baseURL+mod+"/update",
            data: data
            })
    }

    switchStatus(id, mod){
        return this.$http({
            method: "POST",
            url: this.baseURL+mod+"/toggle-status",
            data: id
            })
    }
}


