import * as appProperties from "../../../config.properties";

class ListService{

    constructor($http){
        "ngInject"
        this.name="listService";
        this.http=$http;
    }

    getAllCustomer(customerId,pageNumber,pageSize){
        if (customerId == undefined || customerId == null) {
            customerId = "";
        }
         let requestString = {
            method: 'GET',
            url: appProperties.CONFIG.BASE_URL+'/api/viewAllCustomer',
           
            headers: {
                'content-type': 'application/json'
            },
            params:{
                "pageNumber" :pageNumber,
                "pageSize" : pageSize,
                "customerId":customerId
            }
         }
         return this.http(requestString);

     }

     getCustomerByCustomerId(customerId,pageNumber, pageSize){
        console.log(appProperties.CONFIG.BASE_URL+ '/api/getRequestDetails');
        this.isinNumber = ""
        let requestString={
            method:'POST',
            url:appProperties.CONFIG.BASE_URL+'/api/getRequestDetails',
            headers:{
                'content-type': 'application/json'
            },
            params:{
                "customerId":customerId,
                "modelName":"Customer",
                pageNumber: pageNumber,
                pageSize: pageSize,
                "isinNumber": this.isinNumber
            }
        }
    return this.http(requestString);
    }

     editCustomer(customer){
        
        $(".se-pre-con").show();
        this.http({
            method: 'POST',
            url:appProperties.CONFIG.BASE_URL+'/api/update',
            headers: {
                'content-type': 'application/json'
            },
    
           data:customer

        }).then(function(response){
            
        });

     }

    
     

}

export default ListService


