import * as properties from '../../config.properties'

export default class BenposService{
    constructor($http){
        "ngInject"
        this.$http = $http
        this.baseURL = properties.CONFIG.BASE_URL+"/api/benpos/";
    }

    upload(data){
        return this.$http({
            method: "POST",
            url: this.baseURL+"upload",
            headers:{
                "content-type": undefined
            },
            data: data
        })
    }

    getBenposList(lastRecordId){
        return this.$http({
            method: "GET",
            url:this.baseURL+"all",
            params: {
                lastRecordId: lastRecordId
            }
            })
    }

    searchByISIN(isin){
        return this.$http({
            method: "GET",
            url:this.baseURL+"search/isin",
            params:{
                "isin": isin
            }
        })
    }

    downloadEPH(id){
        return this.$http({
            method: "POST",
            responseType: "arraybuffer",
            url:this.baseURL+"eph/download",
            params:{
                "id": id
            }
        })
    }

    downloadDoc(id){
        return this.$http({
            method: "POST",
            responseType: "arraybuffer",
            url:this.baseURL+"download",
            params:{
                "id": id
            }
        })
    }
}


