import * as properties from '../../../config.properties'
import { throws } from 'assert';
var moment = require('moment');
class CreateController {
  multip;
  constructor($stateParams, crudService, buyBackService) {
    "ngInject"
    this.buyBackService = buyBackService
    this.crudService = crudService
    this.mod = 'buyback';
    this.button=false;
    this.baseURL = properties.CONFIG.BASE_URL+"/api";
    this.multip = 500000
    this.tranche = {
      originalObject:{}
    }

    this.$stateParams=$stateParams;
    if($stateParams.edit){
      this.isEditing=$stateParams.edit;
      this.buyback=$stateParams.buyback;
      this.viewing=$stateParams.view;
      this.chk = $stateParams.chk
      if(this.isEditing)
        this.setStartDate()
    }
    
  }


  searchTranche(){
    console.log("searchby: " + this.buyback.isin)
  }

  buildFormData(data, file){ 
    let formData =new FormData()
    formData.append('data',angular.toJson( data));
    if(file == null || file == undefined){
      return formData
    }
    if(file.length > 1)
      formData.append('document', file);
    else
      formData.append('document', file[0]);


    
    return formData;
  }
  
  calculationOnRate() {
    
    var discPrice = 0;
    // if(typeof this.transaction.cpIssue != 'number'){
    // if(this.transaction.cpIssue.includes(',')){
    //     this.transaction.cpIssue=this.transaction.cpIssue.replace(/,/g,'');
    // }
    // }
    //---
    this.tenorInDays = moment(this.tranche.originalObject.maturityDate, 'DD/MM/YYYY').diff(
      moment(this.tranche.originalObject.valueDate, 'DD/MM/YYYY'), 'days');

    //let discRate = (((100 - this.buyback.discountedPrice) / 100) * (365 / tenorInDays)) * 100;

    var discValue = 0;
    discValue = (this.buyback.cpIssue * this.buyback.discountedPrice) / 100;
    this.buyback.discountedValue = discValue;


    var yieldvalue=0;
    yieldvalue=(this.transaction.cpIssue*this.discRate)/this.transaction.discountedValue;
    //---
    
    

}

  downloadDocument(){
    let stable = true
    this.downloading = true
    /*if(id == null || id == undefined){ // don't need this,button hidden in UI
      this.downloadMsg = "file does not exist"
      
      this.downloading = false
      return
    }*/
    
    if(this.chk || ((this.buyback.createdDate == this.buyback.updatedDate) && this.buyback.status == -1))
      stable = false;
    this.buyBackService
      .downloadDocument(this.buyback, stable)
        .then((response)=>{
          if(response.status == 204){
            this.downloadMsg = "file does not exist"
            this.downloading = false
            return
          }else if(response.status == 200 ||
                response.status == null || 
                response.status == undefined){ 
                let fileName = response.headers("Content-Disposition").split(";").pop().split("filename=", 2).pop()
                
                this.buildAndPushFile(response.data, fileName)
                this.downloading = false
          }else{
            this.downloadMsg = "an unexpected error has occured, please contact administrator"
            return
          }
        })  
  }
  

  buildAndPushFile(result, name){
    // be careful of scopes, might cause file leakages
      let file = new Blob([result], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8" });
      let fileURL = (window.URL || window.webkitURL).createObjectURL(file);
      let link = angular.element('<a/>');
      link.attr({
        href: fileURL,
        download: name
      })[0].click();
  }

  // sub(form){
  //   console.log(this.buyback.buyBackAmount);
  //   if(this.buyback.buyBackAmount % this.multip != 0){
  //     alert(this.buyback.buyBackAmount + " is not multiple of 5 lacs");
  //     return;
  //   }else{
  //     console.log("here else");
  //   }
  // }

  submit(form){
    //debugger;
    if(this.buyback.buyBackAmount % this.multip != 0){
      //console.log("inside if");
      alert("Amount should be multiple of 5 lacs");
      return;
    }

    //console.log("out of if");
    
    this.requesting = true
    try{

     
     // debugger;
      let bufDate = this.buyback.date
      // angular and moment's formats mismatch, look here for any date related bugs
      this.buyback.date =  moment(this.buyback.date, 'DD/MM/YYYY').toDate();
      this.buyback.tranche = this.tranche.originalObject // set these to initial value on dropdown,
      this.buyback.investor = this.investor.originalObject// in case of updates
      this.buyback.units = this.buyback.buyBackAmount/this.multip;

      this.buyback.investor.bankIFSC=null;
      this.buyback.investor.bankAccountNumber=null;
      this.buyback.investor.bankName=null;
     
      if(this.isEditing){  
        
        this
        .buyBackService
          .update(this.buildFormData(this.buyback, this.document))
            .then((response)=>{
             // debugger;
              if(response.data.serviceResponse == 'OK'){
                this.submitResponse = true;
                this.message = response.data.serviceStatus;
              }
              else{
                this.submitResponse = false;
                this.message = response.data.serviceError;
              }
                
              this.buyback.date = bufDate
              this.requesting = false;
              
              this.document = null;
              this.buyback = {};
              this.tranche ={};
              this.buyback.investor = {};
            });
      }else{
        this.buyback.tranche.commercialPaper.customer=null;
        this
        .buyBackService
          .create(this.buildFormData(this.buyback, this.document))
            .then((response, error)=>{
             // debugger;
              console.log("Buyback Create response: "+ response.data);
              if(response.data.serviceResponse == 'OK'){
                this.submitResponse = true;
                this.message = response.data.serviceStatus;
              }else{
                this.submitResponse = false;
                this.message = response.data.serviceError;
              }
              this.buyback.date = bufDate
              this.requesting = false;
              
              for(let key in form){
                if(!key.startsWith("$")) {// keys defined by user
                  form[key].$pristine = true;
                }
              }
              this.document = null;
              this.buyback = {}
              this.tranche ={}
             
            })
      }
    }catch(err){
      this.requesting = false;
    }
  
    
  }

  discPriceChange(){    

    //debugger;
     var discValue = 0;
   
     discValue = (this.buyback.buyBackAmount * this.buyback.discountedPrice) / 100;
     this.buyback.discountedValue = discValue;

    let u ;
    if(this.isEditing){
      u =moment( new Date(this.buyback.date)).format('DD/MM/YYYY');
    }else{
      u  = this.buyback.date;
    }
    
   let a = ( (3650000 / this.buyback.discountedPrice) - 36500);
    // let b = moment(this.tranche.originalObject.maturityDate, 'DD/MM/YYYY').diff(
    //      moment(this.buyback.date, 'DD/MM/YYYY'), 'days');

    let valueDate = u;
    let b = moment(this.tranche.originalObject.maturityDate, 'DD/MM/YYYY')
        .diff(moment(valueDate, 'DD/MM/YYYY'), 'days');
    
         let yieldvalue = a/b;
   this.buyback.yield=parseFloat(yieldvalue.toFixed(2));
  }

  discountedVal(cpIssue, discountedPrice){
    // console.log("2 .Disc Val(): "+ (cpIssue * discountedPrice) / 100);
    return (cpIssue * discountedPrice) / 100;
  }

  discountedRate(discountedPrice, tenorInDays){
    let discRate = (((100 - discountedPrice) / 100) * (365 / tenorInDays)) * 100;
    // console.log("3. Disc Rate : "+ discRate);
    return parseFloat(discRate.toFixed(2));
  }

  calcYield(cpIssue, discountedRate, discountedValue){
    return parseFloat(((cpIssue*discountedRate)/discountedValue).toFixed(2));
  }
  checkDate()
  {
    // var utc = new Date().toJSON().slice(0,10).replace(/-/g,'/');
    // console.log(utc);
    //     if(this.tranche.originalObject.maturityDate > utc){
    //       console.log("inside if");
    //       this.button=true;
    //     }else{
    //       this.button=false;
    //       console.log("inside else");
    //     }    
  }

  // showAlert(){
  //  // debugger;
  //       var maturityDate = moment(this.tranche.originalObject.maturityDate, 'DD/MM/YYYY');
  //       var currentDate = moment(new Date(), 'DD/MM/YYYY');
    
  //       if( maturityDate > currentDate){
  //         return 'red';
  //       } else{
  //         return 'green';
  //       }  
  // }


  setStartDate(){
   // debugger;
    if(this.buyback == undefined)
      this.buyback = {}
      if(this.tranche && this.tranche.originalObject)
      {
        
       // console.log(this.tranche.originalObject);
        //var utc = new Date().toJSON().slice(0,10).replace(/-/g,'/');
        console.log("Maturity date :" + this.tranche.originalObject.maturityDate);
       
        var maturityDate = moment(this.tranche.originalObject.maturityDate, 'DD/MM/YYYY');
      //  var maturityDate = moment(this.tranche.originalObject.maturityDate).subtract(1, 'days').format('DD/MM/YYYY');
        var currentDate = moment(new Date(), 'DD/MM/YYYY');
       
        // Used for calender last date to select
        this.lastDate =  moment(this.tranche.originalObject.maturityDate, 'DD/MM/YYYY').subtract(1, 'days').format('DD/MM/YYYY');
  
        
     if( maturityDate > currentDate){
        this.button=false;
        // console.log("Else Maturity Date "+ this.tranche.originalObject.maturityDate);
     }
     else{
      //console.log("inside if Maturity Date "+ this.tranche.originalObject.maturityDate);
              this.button=true;
     }
          }
        // if(this.tranche.originalObject.maturityDate < utc){
        //   console.log("inside if");
        // }else{
        //   console.log("inside else");
        // }
        this.buyback.startdate = moment(this.tranche.originalObject.valueDate, "DD/MM/YYYY").add(30, 'd').format("DD/MM/YYYY")
    
    let calc = this.tranche.originalObject.isinNumber;
    try{
      if(this.isEditing || calc == undefined || calc == null)
      calc = this.buyback.tranche.isinNumber;

    }catch(err){

    }
    
    this.buyBackService
      .calcTotalWithBuyback(calc)
        .then((response)=>{
          if(this.isEditing){
            this.buybackBalance = (response.data*this.multip)+((this.buyback.status ==0)?this.buyback.buyBackAmount:0)
          }
          else{
            this.buybackBalance = response.data*this.multip
          }
        })
  }

}

export default CreateController;



