import * as properties from '../../config.properties'

export default class InvestorService {
    constructor($http){
        "ngInject"
        this.$http = $http
        this.baseURL = properties.CONFIG.BASE_URL+"/api";
    }


    getInvestorList(lastRecordId){
        return this.$http({
            method: "GET",
            url:this.baseURL+"/investor/all",
            params: {
                lastRecordId: lastRecordId
            }
            })
    }


    getById(id){ // make strong checks on who gets calls this 
        return this.$http({
            method: "POST",
            url:this.baseURL+"/investor/get",
            params:{
                "id": id
            }
            })
    }

    searchByName(name){
        return this.$http({
            method: "GET",
            url:this.baseURL+"/investor/search/name",
            params:{
                "name": name
            }
        })
    }

}


